:root {
  --tiny-text: 0.7rem;
  --small-text: 0.8rem;
}

body {
  font-family: $f-body;
  font-weight: 200;
  font-size: $base-font-size;
  line-height: 1.5;
  color: gc(text);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $f-serif;
  color: gc(text, accent);
  font-weight: 400;
  line-height: 0.85;
  margin: 0 0 0.3em 0;
  letter-spacing: 0.01em;
  
  &:not(:first-child) {
    margin-top: 0.6em;
  }
  .Main__aside & {
    color: gc(text, neutral);
  }
  & + h2,
  & + h3,
  & + h4,
  & + h5,
  & + h6 {
    @include increase-specificity() {
      margin-top: 0;
    }
  }

  &.u-text-sans {
    letter-spacing: 0.2em;
  }
}

h1 {
  @include rfs($f-size-h1);
}
h2 {
  @include rfs($f-size-h2);
}
h3 {
  @include rfs($f-size-h3);
}
h4 {
  @include rfs($f-size-h4);
}
h5 {
  @include rfs($f-size-h5);
}
h6 {
  @include rfs($f-size-h6);
  line-height: 1.2
}

p {
  font-family: $f-body;
  margin: 0 0 1.4em 0;
  //font-size: 0.75rem;
  &:last-child{
    margin-bottom: 0;
  }
}

body,
input,
textarea,
button,
select {
  -webkit-font-smoothing: antialiased;
  text-size-adjust: 100%;
}

a {
  @include link-colors(gc(link, normal), gc(link, hover));
  text-decoration: none;
  font-weight: 600;
  font-family: $f-sans;
}

a:focus {
  color: inherit;
}

hr {
  border: none;
  border-bottom: 1px solid black;
  @include rfs(4rem 0, margin);
  
  &.Container {
    max-width: calc(100% - 2rem);
    @include mq($from: desktop) {
      max-width: calc(100% - 6rem);
    }
    margin-left: auto;
    margin-right: auto;
    @include rfs(4rem, margin-top);
    @include rfs(4rem, margin-bottom);
  }
}

.icon {
  width: 1em;
  height: 1em;
}

blockquote {
  text-transform: none;
  margin: 2em auto;
  max-width: 24em;
  font-style: italic;
}

strong {
  font-weight: bold;
}

.has-large-font-size {
  font-weight: 300;
  line-height: 1.2;
}

.has-link {
  cursor: pointer;
}

// ----------------------------------------------------------------------------
// TEXT UTILITIES
// ----------------------------------------------------------------------------
.u-text-display {
  font-family: $f-display;
}

.u-text-sans {
  font-family: $f-sans;
}

// ALIGNMENT ------------------------------------------------------------------

.u-text-center {
  text-align: center !important;
}

.u-text-left {
  text-align: left !important;
}

.u-text-right {
  text-align: right !important;
}

// VISIBILITY UTILS ------------------------------------------------------------------

.supports-no-cookies {
  display: none;
  body.cookies-not-supported & {
    display: block;
  }
}

.text-center {
  text-align: center;
}