.Badge{
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  color: #fff;;
  font-size: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  background-color: var(--color-ui-secondary);
  span{
    font-size: 0.25em;
    line-height: 1;
    text-transform: uppercase;
    font-family: $f-sans2;
    font-weight: 700;
  }

  &.Badge--sale{
    background-color: #FFE3D4;
    span{
      font-size: 0.35em;
      color:#a9a8a8;
    }
  }
}