.Tile{
  position: relative;
  overflow: hidden;
  .Media__content:before {
    background-color: rgb(156, 131, 111, 0.25);
  }
  img {
    z-index: 1;
  }
  figcaption{
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%,-50%);
    
    display: flex;
    align-items: center;
    flex-direction: column;
    z-index: 3;
  }
  .heading, a{
    color: #fff;
    text-align: center;
  }
  .heading{
    @include rfs(5rem);
  }
}