body[data-view="careers"] {
  .Main__header {
    padding: 0;
    margin-bottom: 0;
    @include mq($until: tablet) {
      margin-top: 1rem;
    }
  }

  .Section--join-the-team {
    font-size: 1.2rem;
    @include mq($from: tablet) {
      @include rfs(1.5rem);
      p {
        line-height: 2;
      }
    }
  }

  .Section--oportunities {
    padding: 1rem;
    background-image: url("~Source/img/insiders_bg.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    &:before,
    &:after {
      content: "";
      position: absolute;
      width: 1em;
      height: 1em;
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      @include rfs(12rem);
    }
    &:before {
      top: -0.4em;
      left: -0.3em;
      background-image: url("~Source/img/flower_deco_2.png");
      transform: scaleY(-1) rotate(20deg);
    }
    &:after {
      bottom: -0.3em;
      right: -0.3em;
      background-image: url("~Source/img/flower_deco_3.png");
    }
    .heading {
      @include mq($until: tablet) {
        font-size: 3.5rem;
      }
    }
    .Loop {
      grid-template-columns: 1fr;
      @include mq($from: tablet) {
        grid-template-columns: 1fr 1fr;
      }
    }
    .wrapper {
      background-color: #fff;
      padding: 1rem;
      @include mq($from: tablet) {
        @include rfs(2rem 4rem, padding);
      }
    }
    .mailto {
      text-align: center;
    }
  }
  .Section--ivy-family {
    .Loop--flex {
      display: flex;
      flex-wrap: wrap;
      column-gap: 0;
      justify-content: center;
    }
    .Card {
      padding: 1rem;
      flex-basis: 50%;
      @include mq($from: tablet) {
        flex-basis: 25%;
      }

      &.highlighted {
        flex-basis: 100%;
        @include mq($from: tablet) {
          flex-basis: calc(100% / 3);
        }
        .Card__media {
          max-width: 20rem;
        }
      }
      row-gap: 1.5rem;
    }
  }
  .bottom_text {
    @include rfs(1.3rem);
    line-height: 2;
  }
}
