.ProductGallery--model,
.ProductGallery--full {
  align-self: flex-start;
  position: relative;
  .Gallery__item {
    width: 100%;
    a {
      display: block;
    }
  }
  img {
    width: 100%;
  }
  .mainImage {
    .product-single__media {
      position: relative;
    }
    img {
      @include absolute(top 0 right 0 bottom 0 left 0);
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
    }
  }
}

.ProductGallery--model {
  position: relative;
  margin-bottom: 1rem;
  @include mq($from: desktop) {
    //padding-top: 3rem;
  }
  .mainImage {
    &:not(.flickity-enabled) {
      .Gallery__item {
        &:not(:first-item) {
          position: absolute;
          opacity: 0;
          visibility: hidden;
        }
      }
    }
  }
}

.ModelsNav {
  position: absolute;
  bottom: 1rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  width: 100%;
  max-width: 36rem;
  padding: 0 0.5rem;
  @include mq($from: tablet) {
    bottom: 8rem;
  }
  .modelItem {
    padding: 0.5rem;
    border-radius: 5rem;
    overflow: hidden;
    display: grid;
    grid-template-columns: 5rem 1fr;
    @include mq($from: tablet) {
      grid-template-columns: 8rem 1fr;
    }
    column-gap: 1rem;
    cursor: pointer;
    position: relative;
    background-color: rgba(255, 255, 255, 0.85);
    @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
      -webkit-backdrop-filter: blur(15px);
      backdrop-filter: blur(15px);
      background-color: rgba(255, 255, 255, 0.6);
    }
    &:hover {
      @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
        -webkit-backdrop-filter: blur(25px);
        backdrop-filter: blur(25px);
        background-color: rgba(255, 255, 255, 0.6);
      }
    }
  }
  .Media__content {
    border-radius: 5rem;
    overflow: hidden;
    &:before {
      padding-top: 100%;
    }
    img {
      object-position: top center;
    }
  }
  .info {
    padding-right: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
  }
  .title {
    font-size: 1.3rem;
    @include mq($from: tablet) {
      font-size: 1.7rem;
    }
  }
  .measures {
    font-size: 1rem;
    @include mq($from: tablet) {
      font-size: 1.3rem;
    }
  }
  .icon {
    position: absolute;
    right: 0.5rem;
    font-size: 1.5rem;
  }
  .modelDropDown {
    display: none;
    &.is-active {
      display: block;
    }
  }
}

.ProductGallery--full {
  max-width: calc(100% - 1rem);
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 1rem;
  background-image: url("~Source/img/gallery_mobile_bg.png");
  background-position: top left;
  background-size: 100% auto;

  @include mq($from: tablet) {
    background: unset;
    max-width: unset;
  }
  @include mq($from: desktop) {
    display: grid;
    grid-template-columns: 1fr 7fr;
    gap: 1rem;
  }
  .mainImage_wrapper {
    position: relative;
  }
  .mainImage {
    display: none;
    &.flickity-enabled {
      display: block;
    }
  }
  .Gallery__nav {
    .previous,
    .next {
      position: absolute;
      width: 1em;
      height: 1em;
      background-color: var(--color-ui-secondary);
      border-radius: 50%;
      top: calc(50% - 0.5em);
      font-size: 2rem;
      .icon {
        font-size: 0.6em;
        color: #fff;
      }
    }
    .previous {
      left: 0.5rem;
    }
    .next {
      right: 0.5rem;
    }
  }
  .flickity-page-dots {
    bottom: 0.5rem;
    .dot:not(.is-selected) {
      background: #fff;
    }
  }
}

.Gallery__thumbs {
  overflow: hidden;
  position: relative;
  .wrapper {
    position: absolute;
    inset: 0;
    overflow: auto;
    scroll-behavior: smooth;
  }
  .item {
    width: 100%;
    margin-bottom: 1rem;
    cursor: pointer;
    figure {
      position: relative;
      background: white;
    }
    figure:before {
      content: "";
      display: block;
      width: 100%;
      padding-top: 150%;
    }
    img {
      @include absolute(top 0 right 0 bottom 0 left 0);
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
}

.Gallery__status {
  display: none;
}

.product-single__media {
  position: relative;
  img {
    @include absolute(top 0 right 0 bottom 0 left 0);
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
  }
}

.product-single__media {
  .Media__content:before {
    padding-top: 150%;
  }
  .Media__content:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 80%;
  }
}

.chocolat-image-wrapper {
  box-shadow: hsla(229, 22%, 14%, 0.06) 0 6px 24px, hsla(229, 22%, 14%, 0.06) 0 12px 36px;
}
