.template-order {
  // @include border(red);
  .Main__content {
    h3 {
      font-size: 1.4rem;
    }
  }
}

.order-details-table {
  border-collapse: collapse;
  margin-bottom: 3rem;
  td,
  th {
    padding: 0.6em;
    border-bottom: 1px solid var(--color-muted-lines);
  }
  tr {
    td,
    th {
      @include mq($from: 900px) {
        &:last-child {
          text-align: center;
        }
      }
    }
  }
  tfoot {
    td {
      &:first-child {
        text-align: right;
      }
    }
  }
}
