.Hero--home{
  @include rfs(7rem, margin-bottom);
  padding-top: 1rem;
  @include mq($from:tablet){
    padding-top: 0;
  }
  .bg{
    
    width: 100%;
    position: absolute;
    z-index: -1;
    background-position: top left;
    height: 55%;
    @include mq($from: tablet){
      height: 70%;
      margin-top: 3rem;
    }
    @include mq($until: tablet){
      background-size: 100% !important;
    }
  }
  .Container{
    margin-top: 1rem;
    display: grid;
    gap: 0.5rem;
    grid-template-columns: 1fr 1fr;
    background-color: #fff;
    
    @include mq($from:tablet){
      grid-template-columns: 2fr 1fr;
      margin-top: 0;
    }
  }
  .Hero__tile{
    background-color: v(color-ui-primary);
    .Media__content:before{
      padding-top: 105%;
    }
    @include mq($until: tablet){ 
      &:first-child{
        margin-right: -1rem;
        grid-column: 1 / span 2;
      }
      &:nth-child(3){ 
        grid-column: 1 / span 2;
        grid-row: 3 / span 1;
        position: relative;
        .Media__content:before{
          padding-top: 90%;
        }
        .Button{
          display: inline-flex !important;
          position: absolute;
          bottom: -3.1em;
        }
      }
    }
  }
  
  figcaption{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    max-width: 90%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
  }
  .text{
    @include rfs(1.35rem);
    max-width: 12em;
    line-height: 1.8;
    margin-bottom: 1rem;
  }
  .Hero__tile:first-child{
    .Media__content:before{
      padding-top: 160%;
    }
    figcaption{
      top: 40%;
    }
    @include mq($from:tablet){
      grid-column: 1 / span 1;
      grid-row: 1 / span 3;
    }
    .text_image{
      position: relative;
      margin-bottom: 1rem;
    }
    .Button{
      @include mq($until:tablet){
        font-size: 1rem;
      }
    }
  }
}