.template-pagePress{
  .Slider{
    @include rfs(5rem, padding-top);
    position: relative;
    .bg{
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      background-position: top left;
      padding-top: 85%;
      @include mq($from: tablet){
        padding-top: unset;
        height: 75%;
      }
      @include mq($until: tablet){
        background-size: 100% !important;
      }
    }
    .Slider__item{
      width: 100%;
    }
    .SliderNav{
      margin-top: 3rem;
      margin-bottom: 3rem;
      display: flex;
      justify-content: space-between;
      svg{
        @include mq($until: tablet){
          width: 100px;
        }
      }
    }
    
  }

  .Section--press{
    .Section__header .heading{
      color: var(--color-text-accent);
    }
  }
  .Section--as-seen-in,
  .Section--as-seen-on{
    @include mq($until: tablet){
      .Container{
        max-width: calc(100% - 4rem);
      }
    }
    .Entry{
      background-color: #fff;
      @include mq($from: tablet){
        display: grid;
        grid-template-columns: 1fr 1fr;
      }
      
    }
    .Entry__figure{
      &:before{
        padding-top: 100%;
      }
    }
    .Entry__details{
      padding: 2rem 0;
      @include mq($from: tablet){
        @include rfs(3rem, padding);
      }
      
      @include rfs(2.5rem);
      h3{
        @include rfs(5.8rem);
      }
      p{
        font-family: $f-serif;
        line-height: 1.1;
        &:not(:last-child){
          margin-bottom: 2rem;
        }
      }
    }
  }
  
  .Section--as-seen-in{
    @include mq($from: tablet){
      .Entry{
        grid-template-areas: "details media";
      }
      .Entry__figure{
        grid-area: media;
      }
      .Entry__details{
        grid-area: details;
        @include rfs(4.3rem);
      }
      .logo{
        height: 3.5rem;
      }
    }
    .Entry__details{
      font-size: 2.6rem;
      @include mq($from: tablet){
        @include rfs(4.3rem)
      }
    }
    .logo{
      height: 2.5rem;
      width: auto;
      @include rfs(3rem, margin-bottom);
    }
    .Button{
      @include mq($until: tablet){
        padding: 0 1.5rem;
        font-size: 0.85rem;
      }
    }
  }
  .Section--as-seen-on{
    .Entry__details{
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
  .FeaturedArticles{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    @include mq($until: tablet){
      margin-bottom: -30%;
    }
    &.has-two-rows{
      @include mq($from: tablet){
        margin-bottom: -5%;
      }
    }
    figure{
      width: 100%;
      @include mq($from: tablet){
        width: calc(100% / 3);
        &:nth-child(4),&:nth-child(5){
          transform: translateY(-50%);
        }
      }
      @include mq($until: tablet){
        &:nth-child(2){
          transform: translateY(-25%) rotate(10deg);
          
        }
        &:nth-child(3){
          transform: translateY(-45%) rotate(-10deg);
        }
        &:nth-child(4){
          transform: translateY(-65%);
        }
        &:nth-child(5){
          transform: translateY(-85%);
        }
      }
      &:hover{
        z-index: 2;
      }
    }
  }
}