.template-pageSize-guide {
  .Main .Container{
    @include mq($until: tablet){
      max-width: 100%;
    }
  }
  .Main__header{
    margin-bottom: 0;
    margin-top: 0;
    h6{
      margin-bottom: 0;
    }
  }
  .Section--OurModels{
    margin-top: 0;
    .Section__header{
      @include rfs(8rem, margin-bottom);
      position: relative;
      overflow: hidden;
      .bg{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        padding-top: 100%;
        background-position: top left;
        background-size: 100%;
        background-repeat: repeat;
        @include mq($from: tablet){
          background-size: 70%;
          padding-top: 50%;
        }
        @include mq($from: desktop){
          background-size: 50%;
        }

      }
    }
    .Section__content{
      @include mq($until: tablet){
        padding: 0 1rem;
      }
    }
    .Cover-gallery{
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      padding:  1rem;
      @include mq($from: tablet){
        padding:  2rem;
        grid-template-columns: repeat(4, 1fr);
      }
      .Cover-gallery__item{
        &:before{
          padding-top: 115%;
        }
        &:hover{
          figcaption{
            opacity: 1;
          }
          img{
            transform: scale(1.05);
          }
        }
        figcaption{
          opacity: 0;
          position: absolute;
          top: 0%;
          left: 0;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          transition: all 400ms ease-out 150ms;
          background-color: rgba(0,0,0,0.15);
        }
        h2,h6{
          color: #fff;
        }
        h2{
          margin-bottom: 0.2em;
          @include rfs(3rem, margin-top);
        }
        h6{
          @include rfs(3.5rem, margin-bottom);
          font-family: $f-sans;
          text-transform: uppercase;
          @include rfs(1.3rem);
        }
      }
    }

    .Model{
      @include mq($from: tablet){
        display: grid;
        grid-template-columns: 1fr 1.6fr;
        grid-template-areas: "image content";
        align-items: center;
        column-gap: 2rem;
      }
      @include mq($from: 1200px){
        @include rfs(8rem, column-gap);
      }
      &:not(:last-child){
        @include rfs(6rem, margin-bottom);
      }
    }

    figure{
      position: relative;
      overflow: hidden;
      &:before {
        content:"";
        display: block;
        width: 100%;
        padding-top: 140%;
      }
      img {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-position: center;
        object-fit: cover;
        transition: transform 1000ms ease-out;
      }
      
    }
    .Model__photo{
      grid-area: image;
      align-self: start;
    }
    .wrapper{
      grid-area: content;
      display: flex;
      flex-direction: column;
    }
    .shop-the-look{
      order: 2;
      font-style: italic;
      font-weight: 300;
      @media (min-width: 1024px){
        order: unset;
        text-align: right;
      }
    }
    .featured{
      display: grid;
      grid-template-columns: 1fr 1fr;
      order: 3;
      margin-left: -0.5rem;
      margin-right: -0.5rem;
      @media(min-width: 1024px){
        display: flex;
        width: calc(100% + 10rem);
        margin-left: -9.5rem; 
        @include margin-top(3rem);
        order: unset;
      }
      
      .featured__item{
        display: block;
        overflow: hidden;
        margin: 0.5rem;
        z-index: 10;
        @media(min-width: 1024px){
          flex-basis: calc(100% / 4);
        }
        &:hover{
          img{
            transform: scale(1.1);
          }
        }
      }
    }
    .bio{
      order: 1;
      padding: 1rem 0;
      color: #6b6b6b !important;
      @include mq($from: tablet){
        font-size: 1.15rem;
        padding: 0;
        order: unset
      }
      @include mq($from: 1200px){
        padding: 0 2rem;
      }
      h2{
        font-size: 5rem;
        display: flex;
        column-gap: 0.8em;
        flex-direction: column;
        @include mq($from: tablet){
          flex-direction: row;
          align-items: flex-end;
        }
      }
      .instagram_link{
        font-size: 0.5em;
        display: inline-block;
        margin-bottom: 0.2em;
        a{
          font-family: inherit;
          font-weight: 400;
        }
      }
      .about{
        margin-bottom: 2rem;
        @include rfs(1.5rem);
      }
      .Button[data-style*="solid"]{
        padding: 0 2.5em;
        font-size: 1.1rem;
        min-height: 3.5em;
        font-weight: 500;
        @include mq($until: tablet){
          width: 100%;
          padding: 0 1em;
        }
      }
    }
    .Measurements{
      @include rfs(3rem, margin-top);
      .Measurements__item {
        padding-top: 1.1rem;
        padding-bottom: 1.1rem;
        @include rfs(3rem, padding-left);
        @include rfs(3rem, padding-right);
        text-transform: uppercase;
        font-weight: 600;
        text-align: left;
        position: relative; 
        letter-spacing: 2.25px;
        font-size: 0.9rem;
        display: flex;
        justify-content: space-between;
        column-gap: 1rem;
        span {
          font-weight: 400;
          flex-basis: 6rem;
        }
        &:nth-child(odd){
          background-color: #F6F6F4;
        }
      }
    }
  }
}
