//LOVERS COLLECTION LANDING PAGE

.template-pageLovers-collection-landing{
  .responsive_img{
    width: 100%;
    position: relative;
    &:before {
      content:"";
      display: block;
      width: 100%;
      padding-top: 100%;
    }
    img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-position: center;
      object-fit: contain;
    }
  }
  header{
    padding: 2.5rem 2rem;
    .responsive_img{
      max-width: 30rem;
      margin: 0 auto;
      &:before{
        padding-top: 14%;
      }
      img{
        object-fit: contain;
      }
    }
  }
  .Container {
    @media (min-width: 1024px) {
      width: 100%;
      max-width: calc(100% - 6rem);
      margin: 0 auto;
    }
  }
  .Container--wide{
    @media (min-width: 1920px) {
      max-width: 112rem;
    }
  }
  .Section--loversCollectionMain{
    margin-top: 0;
    .deco {
      background: #FAF4F1;
    }
    main{
      display: grid;
      gap: 3rem;
    }
    .wrapper{
      max-width: 34rem;
      margin: 0 auto;
    }
    p{
      color: #4C4C4C !important;
    }
    .title{
      font-size: 1.7rem;
      @media (min-width: 1024px){
        font-size: 2.3rem;
      }
      letter-spacing: 0.1em;
      strong{
        font-size: 4rem;
        display: inline-block;
        width: 100%;
        font-weight: 900;
        font-style: italic;
        letter-spacing: initial;
        margin-top: 1rem;
        @media (min-width: 1024px){
          font-size: calc(1em + 1vw);
          margin-top: 0;
        }
        @media (min-width: 1500px){
          font-size: calc(1.2em + 1vw);
          margin-top: 0;
        }
      }
      p{
        margin-bottom: 0.5rem;
      }
      @media (max-width: 1024px){
        text-align: center;
      }
    }
    .intro{
      font-size: 1.45rem;
      margin-bottom: 3rem;
      @media (max-width: 1024px){
        text-align: center;
        font-size: 1.2rem;
      }
    }
    .disclaimer{
      font-size: 1rem;
    }
    .steps{
      li{
        display: grid;
        grid-template-columns: 3rem 1fr;
        column-gap: 1rem;
        margin-bottom: 1.5rem;
        font-size: 1.1rem;
        font-weight: 600;
      }
      .counter{
        display: inline-block;
        padding: 1rem;
        background: #FFC3C2;
        line-height: 1;
        border-radius: 50%;
        width: 3rem;
        height: 3rem;
        font-size: 1.3rem;
        font-style: italic;
        font-weight: 800;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      p{
        margin-bottom: 0;
      }
    }
    [class*='klaviyo-form']{
      [class*='View__FormView']{
        margin: 0;
        max-width: 100%;
      }
      [class*='FormComponent']{
        padding: 6px 10px !important;
      }
      
      *, ::placeholder{
        font-family: 'Spectral', serif !important;
      }
      input{
        border: 1px solid #F1E2E2 !important;
        min-height: 4.5rem;
      }
      button{
        min-height: 3.2rem;
        font-family: montserrat !important;
        font-weight: 900 !important;
        font-size: 1.3rem !important;
        letter-spacing: 0.1em !important;
      }
    }
  }
  .Main__layoutA{
    grid-template-columns: 1fr;
    @media (min-width: 1024px){
      grid-template-columns: 1fr 1fr;
    }
    @media (min-width: 1250px){
      grid-template-columns: 1.45fr 1fr;
    }
    padding-bottom: 3rem;
    margin-bottom: 3rem;

    .Column{
      position: relative;
      .responsive_img {
        @media (min-width: 1024px){
          margin-bottom: 5rem;
        }
        &:before{
          padding-top: 105%;
        }
        &:nth-child(2){
          position: absolute;
          @media (max-width: 1024px){
            display: none;
          }
        }
        figcaption{
          background-position: center;
          background-repeat: no-repeat;
          background-size: contain;
          position: absolute;
          width: 30%;
          height: 30%;
          bottom: -7.5%;
          left: -5.5%;
        }
      }
      &:nth-child(even){
        padding: 1rem;
        @media (min-width: 1024px){
          padding: 5rem 2rem 1rem 2rem;
        }
        
      }
      
    }
  }
  main.Main__layoutB{
    grid-template-columns: 1fr;
    grid-template-areas: "content" "image";
    margin-bottom: 3rem;
    @media (min-width: 1024px){
      grid-template-columns: 1fr 1fr;
      grid-template-areas: "image content";
    }
    @media (min-width: 1250px){
      grid-template-columns: 1.45fr 1fr;
    }
    .Column{
      
      &:first-child{
        grid-area: image;
      }
      &:last-child{
        grid-area: content;
        padding: 1rem;
        @media (min-width: 1024px){
          padding: 2rem;
        }
      }
      .responsive_img {
        opacity: 1;
        &:before{
          padding-top: 145%;
        }
        @media (min-width: 1024px){
          opacity: 0;
          &:before{
            padding-top: 68%;
          }
        }
        @media (min-width: 1650px){
          &:before{
            padding-top: calc(75% + 5 * 1%);
          }
        }
        @media (min-width: 1800px){
          &:before{
            padding-top: 90%;
          }
        }
      }
    }
  }
  footer {
    text-align: center;
    padding: 2rem;
    background-color: #FAF4F1;
    .Nav{
      justify-content: center;
    }
    .copyright {
      margin-top: 2em;
      text-transform: uppercase;
      letter-spacing: 3px;
      font-family: raleway;
      font-size: 11px;
      a {
        color: #4C4C4C !important;
      }
    }
  }
}