.Media {
  .Media__content {
    position: relative;
    &:before {
      content: '';
      display: block;
      width: 100%;
    }
    .media-player-wrapper,
    video,
    img {
      @include absolute(top 0 right 0 bottom 0 left 0);
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
  // --------------------------------------------------------------------------
  // LAYOUT OPTIONS
  // --------------------------------------------------------------------------
  &[data-layout*='overlay'] {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: 'content';
    .Media__details {
      grid-area: content;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      z-index: 2;
    }
    .Media__content {
      grid-area: content;
    }
  }

  &[data-layout*='contain'] {
    .Media__content {
      img {
        object-fit: contain;
      }
    }
  }

  // --------------------------------------------------------------------------
  // IMAGE RATIO OPTIONS
  // --------------------------------------------------------------------------
  &[data-ratio='1/1'] {
    .Media__content {
      &:before {
        padding-top: 100%;
      }
    }
  }
  &[data-ratio='2/1'] {
    .Media__content {
      &:before {
        padding-top: 50%;
      }
    }
  }
  &[data-ratio='1/2'] {
    .Media__content {
      &:before {
        padding-top: 120%;
      }
    }
  }
  &[data-ratio='1/3'] {
    .Media__content {
      &:before {
        padding-top: 130%;
      }
    }
  }
}
