.MediaWrapper{
  position: relative;
  margin: 1rem 0;
  &:before{
    content: "";
    display: block;
    width: 100%;
    padding-top: 56.25%;
  }
  img,iframe{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-position: center;
    object-fit: cover;
  }
}