.template-article {
  .Article__header {
    position: relative;
    @include rfs(4rem, margin-bottom);
    text-align: center;
  }
  .main-image {
    position: relative;

    &:before {
      content: '';
      display: block;
      width: 100%;
      padding-top: 90%;
    }
    img {
      @include absolute(top 0 right 0 bottom 0 left 0);
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    figcaption{
      padding: 1rem;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: rgba(0, 0, 0, 0.1);
      h1{
        max-width: 50rem;
        margin: 0 auto;
        color: #fff;
        @include mq($from: tablet){
          @include rfs(12rem);
        }
      }
    }
  }
  .Article__sequentialNav {
    .btn {
      position: absolute;
      top: 0.8em;
      background-color: #fff;
      border-radius: 50%;
      width: 3em;
      height: 3em;
      box-shadow: 0 1px 8px rgba(0, 0, 0, 0.05);
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1rem;
      @include mq($from: tablet) {
        top: 1.5em;
        @include rfs(1.8rem);
      }
    }
    .prev {
      left: 1em;
      @include mq($from: tablet) {
        left: 2em;
      }
    }
    .next {
      right: 1em;
      @include mq($from: tablet) {
        right: 2em;
      }
    }
    &.no-cover-image {
      display: flex;
      justify-content: space-between;
      margin-bottom: -3rem;
      .btn {
        position: relative;
        top: 0;
      }
      .prev {
        order: 1;
        left: 0;
      }
      .next {
        order: 2;
        right: 0;
      }
    }
  }
  .Article__details {
    max-width: 50rem;
    margin: 0 auto;
    .meta {
      font-family: $f-sans2;
      text-transform: uppercase;
      text-align: center;
      @include rfs(3rem, margin-bottom);
    }
  }
}
