.template-login,
.template-register,
.template-reset_password,
.template-activate_account{
  #customer_login{
    margin-bottom: 2rem;
  }
  .input-row{
    margin-bottom: 1rem;
  }
  label{
    font-size: 0.8em;
  }
  input{
    width: 100%;
  }
  p{
    text-align: center;
    margin-top: 1em;
  }
  .submit{
    .Button{
      margin-bottom: 1rem;
    }
  }
  #RecoverPasswordForm{
    text-align: center;
  }
}

.template-account{
  .Main__content{
    @include mq($from: desktop){
      display: grid;
      grid-template-columns: 1fr min-content;
      column-gap: 2rem
    }
  }
  .orders-container{
    margin-bottom: 3rem;
  }
  .orders {
    table {
      border-collapse: collapse;
      td,
      th {
        text-align: center;
        padding: 0.9em 1.3em;
        font-size: 0.9rem;
      }
    }
  }
}