ul,
ol {
  &.is-style-block-list {
    list-style-position: inside;
    padding: 0 !important;
    margin: 0 !important;
    li {
      background: white;
      box-shadow: 0 5px 34px 0 rgba(0, 0, 0, 0.08);
      padding: 1em;
      margin: 1em 0;
      border-left: 3px solid gc(ui, blue);
    }
  }
}

// ----------------------------------------------------------------------------
// Measurements
// ----------------------------------------------------------------------------
.Measurements {
  font-family: $f-sans;
  margin: 1em 0;
  > div {
    display: flex;
    margin: 0.2em 0;
  }
  dt {
    font-weight: bold;
    text-transform: uppercase;
    margin-right: 0.5em;
  }
  &[data-style*='primary'] {
    @include rfs(1.6rem);
    padding-top: 1rem;
    padding-bottom: 1rem;
    border-top: 1px solid var(--color-ui-muted);
    border-bottom: 1px solid var(--color-ui-muted);
  }
  &[data-style*='secondary'] {
    font-size: 0.9rem;
  }
}
