// ==========================================================================
// Display utilities
// ==========================================================================

.hide,
.is-hidden,
.u-hidden {
  display: none !important;
}

.u-inline {
  display: inline !important;
}

.u-inlineBlock {
  display: inline-block !important;
}

.u-block {
  display: block !important;
}

.u-table {
  display: table !important;
}

.u-tableCell {
  display: table-cell !important;
}

.u-posRelative {
  position: relative;
}

.u-posAbsolute {
  position: absolute;
}
