.Section {
  @include rfs(5rem, margin-top);
  @include rfs(8rem, margin-bottom);
  &:last-child {
    margin-bottom: 0;
  }
  &[data-style*='background'] {
    margin-top: 0;
    margin-bottom: 0;
    @include rfs(3rem, padding-top);
    @include rfs(2rem, padding-bottom);
  }
  &.lazyload,
  &.lazyloaded {
    background-size: cover;
    background-repeat: no-repeat;
  }
}

.Section__header {
  text-align: center;
  @include rfs(4rem, margin-bottom);
  .heading{
    color: v(color-text-pink);
    @include mq($until: tablet){
      font-size: 4.5rem;
    }
    
  }
}


// ----------------------------------------------------------------------------
// Text block
// ----------------------------------------------------------------------------

.Section--TextBlock{
  @include rfs(2.2rem);
  .Section__content{
    padding: 1rem;
  }
}

// ----------------------------------------------------------------------------
// Press Block
// ----------------------------------------------------------------------------

.Section--press{
  padding: 2rem 0;
  @include rfs(3rem !important, margin-bottom);
  .Loop{
    grid-template-columns: repeat(2, 1fr);
    @include mq($from: tablet){
      grid-template-columns: repeat(auto-fill, minmax(12rem, 1fr));
    }
    @include mq($from: desktop){
      grid-template-columns: repeat(5, 1fr);
    }
  }
  .item{
    margin: 0 auto;
    width: 100%;
    max-width: 13rem;
    text-transform: lowercase;
    white-space: nowrap;
    position: relative;
    &:before{
      content: "";
      display: block;
      width: 100%;
      padding-top: 35%;
    }
    img{
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-position: center;
      object-fit: contain;
    }
  }
}

// ----------------------------------------------------------------------------
// Related Products
// ----------------------------------------------------------------------------

.Product__relatedProducts{
  .Section__header{
    text-transform: uppercase;
    text-align: left;
    h6{
      font-family: $f-sans2;
    }
  }
}

// ----------------------------------------------------------------------------
// Related Products
// ----------------------------------------------------------------------------


.Section--ig-feed{
  .Section__header h3{
    text-transform: uppercase;
    font-family: $f-sans;
    font-weight: 200;
  }
}