.Drawer {
  @include fixed(top 0 right 0 bottom 0 left 0);
  z-index: 90;
  background: hsla(104, 100%, 100%, 0.8);
  text-align: left;
  .backdrop {
    @include absolute(top 0 right 0 bottom 0 left 0);
    z-index: 2;
  }

  &.Drawer--left {
    display: none;
    .Drawer__inner {
      background-color: #fff;
    }
    .close-drawer {
      position: absolute;
      right: 1rem;
      top: 0.5rem;
      text-decoration: underline;
      cursor: pointer;
      font-size: 0.9rem;
      .icon {
        font-size: 1.5rem;
      }
    }
  }
}

.Drawer__inner {
  background: #fff;
  @include absolute(top 0 bottom 0);
  z-index: 3;
  width: 90%;
  max-width: 30em;
  box-shadow: hsla(205, 44%, 8%, 0.1) 0 0 51px;
  @include rfs(2rem, padding);
  overflow-y: scroll; /* has to be scroll, not auto */
  -webkit-overflow-scrolling: touch;
}

.Drawer__header {
  margin-bottom: 3em;
  h3 {
    font-family: $f-sans;
    @include rfs(2rem);
    font-weight: 800;
  }
}

.Drawer--minicart {
  max-height: 100vh;
  .Drawer__header {
    position: relative;
    text-align: center;
    margin-bottom: 2rem;
    h3 {
      font-size: 3.1rem;
      margin-bottom: 0;
      outline: none;
    }
    .Button {
      position: absolute;
      top: 0;
      right: 0;
      line-height: 1;
      padding: 0;
      width: 2.5rem;
      height: 2.5rem;
      background-color: var(--color-ui-secondary);
      color: white;
      min-height: unset;
      font-size: 1.5rem !important;
      font-weight: 300;
      border-radius: 50%;
      border: none;
    }
  }
  .Drawer__inner {
    padding: 1.2rem 1.6rem;
    max-width: 25rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow: auto;
    @include mq($until: desktop) {
      padding: 1rem 1rem 1rem 0;
    }
  }
}
