.Minicart__content {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  height: 100%;
  overflow: auto;
  @include mq($until: desktop) {
    padding-left: 0.5rem;
  }

  .Button {
    font-size: 1.5rem;
  }
}
@include mq($until: desktop) {
  .Minicart__footer {
    padding-left: 0.5rem;
  }
}

.Loop--minicart {
  position: relative;
  display: block;
  color: gc(text, neutral);
  font-weight: 400;
  width: 100%;
  margin: 0;

  .Card {
    position: relative;
    @include mq($until: desktop) {
      margin-left: 0.6rem;
      margin-top: 0.6rem;
    }
    &:not(:last-child) {
      padding-bottom: 1rem;
      margin-bottom: 1rem;
      // border-bottom: 1px solid gc(muted);
    }
    .Card__details {
      display: block;
    }
    .Card__title {
      font-family: $f-serif;
      font-weight: 800;
      font-size: 1.2rem;
      line-height: 1.2;
      margin-bottom: 0.8em;
      small {
        display: block;
        color: gc(text, muted);
        font-size: 0.6em;
        text-transform: none;
        letter-spacing: 0;
      }
    }
  }

  .Card__figure {
    margin-bottom: 1rem;
    overflow: hidden;
    position: relative;
    //border-bottom: 2px solid gc(muted);
    background-color: white;
    &:before {
      content: "";
      display: block;
      width: 100%;
      padding-top: 60%;
    }
    img {
      @include absolute(top 0 right 0 bottom 0 left 0);
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  .quantity_and_amount {
    text-align: left;
    display: flex;
    flex-direction: column;
    height: 100%;
    align-items: flex-start;
    .amount {
      @include rfs(1.3rem);
      text-align: left;
      font-family: $f-sans2;
      font-size: 1.2rem;
      color: var(--color-ui-secondary);
      font-weight: 700;
    }
    .quantity {
      white-space: nowrap;
      display: inline-block;
      margin-top: auto;
      font-size: 0.8rem;
      display: flex;
      align-items: center;
      padding: 0.5em;
      border: 1px solid rgba(0, 0, 0, 0.2);
      span {
        padding: 0 1em;
      }
    }
    button {
      background: 0;
      font-family: monospace;
      border: none;
      padding: 0 1.1em;
    }
  }

  .empty-loop {
    // color: gc(text, muted);
    // font-size: ms(2);
    text-align: center;
  }
  .delete-line {
    padding: 0.2em;
    position: absolute;
    top: -0.3em;
    left: -0.2em;
    width: 2.5rem;
    height: 2.5rem;
    background-color: white;
    border-radius: 50%;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.11);
    z-index: 100;
    @include mq($from: desktop) {
      box-shadow: unset;
      top: 0.3rem;
      left: 0.5rem;
    }

    .icon {
      color: black;
      font-size: 1.5rem;
    }
  }
}

ul.MiniCart__totals {
  text-align: center;
  padding: 0.5rem 0;
  list-style: none;
  border-top: 1px solid var(--color-ui-primary);
  .subtotal {
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: 0.1em;
    margin-bottom: 0.4em;
    display: flex;
    justify-content: center;
    .total {
      font-weight: bold;
      margin-left: 0.5em;
      text-align: right;
      font-family: $f-body;
    }
    .label {
      text-align: left;
      font-family: $f-body;
      font-weight: 500;
    }
  }

  .notes {
    color: gc(text, neutral);
    text-align: center;
  }
  .disclaimer {
    font-size: 0.8rem;
    font-weight: 700;
  }
}
.Button--checkout {
  margin-bottom: 0.3em;
}
.Button--checkout,
.Button--view-cart {
  width: 100%;
  padding: 0 1em;
}
.shipping-at-checkout {
  text-align: center;
  margin-top: 0.5em;
  font-size: 0.8rem;
}

.Button--minicart {
  position: relative;
  .icon {
    color: #000;
    margin: 0 !important;
  }
}

.Loop--minicart {
  //height: 50vH;
  overflow-y: auto;
  margin-bottom: 1.5rem;
  .Card--product {
    grid-template-columns: 0.5fr 1fr;
    background: white;
    column-gap: 1rem;
    .Card__figure {
      margin-bottom: 0px;
      &:before {
        padding-top: 150%;
      }
    }
    .Card__details {
      display: flex;
      flex-direction: column;
      text-align: left;
    }
  }
}

//AB TEST

.Minicart__content {
  .Button--checkout {
    display: none;
  }

  body[abtest="true"] & {
    .Button--checkout {
      display: inline-flex;
    }
    .Button--view-cart {
      min-height: 2.5em;
      font-size: 1.3rem;
    }
  }
}
