.Section--behind-the-brand {
  position: relative;
  &:before {
    content: "";
    position: absolute;
    @include rfs(15rem);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    z-index: -1;
    background-image: url("~Source/img/flower_deco_3.png");
    width: 1.2em;
    height: 1em;
    top: 6%;
    left: -0.5rem;
  }
  .mainImage {
    max-width: 65rem;
    margin: 0 auto 2rem auto;
    position: relative;
    @include mq($from: tablet) {
      margin: 0 auto;
    }
    &:before {
      content: "";
      display: block;
      width: 100%;
      padding-top: 100%;
    }
    &:after,
    img {
      position: absolute;
      border-top-left-radius: 50%;
      border-top-right-radius: 50%;
      max-width: calc(100% - 1rem);
      margin: 0 auto;
      width: 100%;
      height: 100%;
      margin-left: 1rem;
      @include mq($from: tablet) {
        max-width: calc(100% - 5rem);
      }
    }
    &:after {
      content: "";
      display: block;
      top: 0.5rem;
      left: -1rem;
      right: 1rem;
      bottom: -0.5rem;
      background-color: v(color-ui-primary);
      @include mq($from: tablet) {
        top: 2rem;
        left: -3rem;
        right: 3rem;
        bottom: -2rem;
      }
    }
    img {
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      object-position: center;
      object-fit: cover;
      z-index: 1;
    }
    figcaption {
      position: absolute;
      top: -2rem;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      text-align: center;
      font-size: 5.5rem;
      @include mq($from: tablet) {
        max-width: 3em;
        @include rfs(8rem);
        top: -1rem;
        left: 0;
        transform: unset;
        text-align: left;
      }
      h2 {
        font-size: inherit;
        line-height: 0.75;
      }
      z-index: 1;
    }
  }
  .Content__main {
    margin-bottom: 1rem;
    position: relative;
    @include mq($from: tablet) {
      @include rfs(9rem, margin-bottom);
    }
    .deco {
      position: relative;
      max-width: 65rem;
      margin: 0 auto;
      &:after {
        content: "";
        position: absolute;
        @include rfs(22rem);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        z-index: 1;
        background-image: url("~Source/img/flower_deco_4.png");
        width: 0.8em;
        height: 2em;
        bottom: 0;
        right: -14%;
        transform: translateY(-25%);
      }
    }
    .aside {
      text-transform: uppercase;
      letter-spacing: 0.2em;
      padding-bottom: 0.5em;
      display: block;
      text-align: center;
      font-size: 0.8rem;
      @include mq($from: tablet, $until: 1200px) {
        display: none;
      }
      @include mq($from: 1200px) {
        letter-spacing: 0.25em;
        font-size: 1rem;
        position: absolute;
        top: 45%;
        right: 0;
        transform: translateX(49%) rotate(-90deg);
        border-bottom: 1px solid var(--color-ui-secondary);
      }
    }
    .Button--play {
      position: absolute;
      top: 60%;
      left: 50%;
      transform: translateX(-50%);
      width: 1em;
      height: 1em;
      @include rfs(11.5rem);
      z-index: 1;
      color: #fff;
      border-radius: 50%;
      background-color: rgba(255, 255, 255, 0.85);
      @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
        -webkit-backdrop-filter: blur(17px);
        backdrop-filter: blur(17px);
        background-color: rgba(255, 255, 255, 0.25);
      }
      .icon {
        font-size: 0.25em;
      }
    }
  }
  .Modal__content {
    .Media__content:before {
      padding-top: 56.25%;
    }
  }
  .Content__secondary {
    text-align: center;
    position: relative;
    header {
      @include rfs(3rem, margin-bottom);
    }
    .bg {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      left: 0;
      height: 75%;
      width: 100%;
      z-index: -1;
      @include mq($from: tablet) {
        top: -15rem;
        height: 100%;
      }
      @include mq($until: tablet) {
        background-size: 100% !important;
      }
    }
    .heading {
      font-size: 5.5rem;
      margin-bottom: 0.2em;
      color: #fff;
      @include mq($from: tablet) {
        font-size: 10rem;
      }
    }
    .Slider__item {
      width: 100%;
      @include mq($from: tablet) {
        width: calc(100% / 3);
      }
      figure {
        position: relative;
        &:before {
          content: "";
          display: block;
          width: 100%;
          padding-top: 100%;
        }
        img {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          object-position: center;
          object-fit: cover;
          margin: auto;
          border-radius: 50%;
          border-color: v(color-ui-primary);
          border-style: solid;
          width: 100%;
          height: 100%;
          @include rfs(1.4rem, border-width);
          @include mq($from: tablet) {
            width: 80%;
            height: 80%;
          }
        }
      }
      figcaption {
        display: none;
      }
      &.is-selected {
        img {
          width: 100%;
          height: 100%;
          font-size: 1.2rem;
          @include mq($from: tablet) {
            font-size: 1.9rem;
          }
        }
      }
    }
    .flickity-button,
    .flickity-page-dots {
      display: none;
    }
  }
  .SliderNav {
    @include rfs(3rem, margin-top);
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    @include mq($from: tablet) {
      justify-content: space-around;
    }
    .caption {
      margin-top: 2rem;
      @include rfs(2rem);
      text-align: center;
      order: 3;
      @include mq($from: tablet) {
        max-width: calc(100% / 3);
        order: unset;
        margin-top: 0;
      }
    }
    .Button {
      @include rfs(5rem);
      margin-top: 0.3em;
      order: 1;

      @include mq($until: tablet) {
        width: 50%;
      }
      svg {
        width: 50%;
        color: black;
      }
      path {
        stroke: var(--color-ui-secondary);
      }
      &.button--previous {
        svg {
          margin-left: auto;
          margin-right: 1rem;
        }
      }
      &.button--next {
        svg {
          margin-right: auto;
          margin-left: 1rem;
        }
      }
      @include mq($from: tablet) {
        order: unset;
        svg {
          width: 100%;
        }
      }
    }
    .icon {
      width: 5em;
    }
  }
}
