.template-pageInsiders {
  .Main__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    span,
    span a {
      text-transform: uppercase;
      font-family: $f-sans2;
    }
    span {
      margin-top: 1em;
    }
    span a {
      text-decoration: underline;
      color: var(--color-ui-secondary);
    }
  }
  .Main__header,
  .Section__header {
    p {
      @include rfs(1.5rem);
    }
  }

  .Main__content {
    max-width: 87rem;
  }

  .Section__header {
    .heading {
      margin-bottom: 0.6em;
    }
  }
  .Section--good-stuff {
    background-image: url("~Source/img/insiders_bg.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 1rem;
    position: relative;
    margin-left: 1rem;
    margin-right: 1rem;
    .wrapper {
      background-color: #fff;
      padding: 1rem;
      @include mq($from: tablet) {
        @include rfs(2rem 3rem, padding);
      }
    }
    .Section__content {
      text-align: center;
      @include mq($from: tablet) {
        @include rfs(0 3rem 3rem 3rem, padding);
      }
      li {
        display: grid;
        grid-template-columns: 0.3fr 1fr 0.2fr;
        align-items: center;
        column-gap: 1rem;
        font-size: 1.15rem;
        margin-bottom: 1em;
        @include mq($from: tablet) {
          font-size: 1.5rem;
        }

        .line {
          display: block;
          position: relative;
          &:before {
            content: "";
            display: block;
            width: 100%;
            height: 1px;
            background-color: #ceb4a3;
          }
        }
        span,
        em {
          white-space: nowrap;
        }
        span:first-child {
          justify-self: flex-end;
        }
        em {
          font-style: unset;
          justify-self: left;
        }
      }
    }
  }

  .Section--how-to {
    text-align: center;
    margin-left: 1rem;
    margin-right: 1rem;
    .Section__header {
      h2 {
        color: var(--color-text-accent);
      }
    }
    .Loop {
      display: grid;
      grid-template-columns: 1fr;
      gap: 2rem;
      @media (min-width: 768px) {
        grid-template-columns: repeat(3, 1fr);
      }
    }
    .Tile {
      @include rfs(2.5rem 1rem, padding);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border: 1px solid var(--color-ui-grey);
      text-transform: uppercase;
      .tile__title {
        @include rfs(1.3rem);
        margin-bottom: 1em;
        line-height: 1.3;
        letter-spacing: 0.1em;

        word-break: normal;
        width: 100%;
      }
      .points {
        color: #c79085;
        font-size: 1.1rem;
        font-family: $f-sans2;
      }
    }
  }

  .Section--rules {
    background-image: url("~Source/img/insiders_bg.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 1rem;
    position: relative;
    .Section__content {
      background-color: #fff;
      padding: 1rem 1rem 2rem;
      @include rfs(1.5rem);
    }
    ul {
      text-align: left;
      //max-width: 50rem;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .Section--refer-a-friend {
    text-align: center;

    .Loop {
      grid-template-columns: 1fr;
      @include mq($from: tablet) {
        grid-template-columns: 1fr 1fr;
      }
      gap: 2rem;
    }
    .Coupon {
      background-image: url("~Source/img/insiders_bg.jpg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      padding: 1rem;
      .inner {
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        @include rfs(5rem 3rem, padding);
      }
    }
    span {
      @include rfs(2.1rem);
      &:first-child {
        margin-bottom: 2em;
      }
      &:last-child {
        @include rfs(1.8rem);
        color: var(--color-ui-secondary);
      }
    }
  }

  .section--cta {
    position: relative;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: white;
    text-align: center;
    @include padding(3rem);
    .content {
      background: white;
      @include padding(3rem);
      .wrapper {
        max-width: 1114px;
        &::after {
          content: unset !important;
        }
      }
      .not-logged {
        display: grid;
        grid-template-columns: 1fr;
        column-gap: 3rem;
        row-gap: 1rem;
        align-items: center;
        justify-items: center;
        @media (min-width: 1024px) {
          //text-align: left;
          grid-template-columns: 1fr max-content;
          justify-items: left;
        }
      }
    }
    h2 {
      margin-bottom: 0;
      @include rfs(3rem);
    }
    .button {
      display: block;
      @include padding-left(3rem);
      @include padding-right(3rem);
      padding-top: 1em;
      padding-bottom: 1em;
      font-weight: bold;
      @include rfs(1rem);
      line-height: 1.2;
      &:hover {
        color: white;
      }
    }
    .deco {
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      position: absolute;
      width: 20%;
      padding-top: 20%;
      @media (min-width: 1200px) {
        width: 12rem;
        padding-top: 12rem;
      }
      &:first-of-type {
        transform: scaleX(-1);
        left: -1.5rem;
        top: -1.5rem;
        @media (min-width: 768px) {
          left: -2.5rem;
          top: -2rem;
        }
        @media (min-width: 1024px) {
          left: -4rem;
          top: -4rem;
        }
      }
      &:last-of-type {
        transform: rotate(180deg);
        right: -2.5rem;
        bottom: -1rem;
        @media (min-width: 768px) {
          right: -3.5rem;
          bottom: -3rem;
        }
        @media (min-width: 1024px) {
          right: -5rem;
          bottom: -4rem;
        }
        @media (min-width: 1600px) {
          right: -13rem;
          bottom: -6rem;
        }
      }
    }
  }
}
