.template-cart {
  .Column {
    margin-bottom: 2rem;
  }
  .Column__header {
    border-bottom: 3px solid var(--color-ui-secondary);
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
    h6 {
      font-weight: 700;
      margin-bottom: 0;
      padding-bottom: 0.5rem;
      font-size: 2.5rem;
    }
    .cart-items-count {
      @include rfs(1.5rem);
      font-style: italic;
    }
  }
  form {
    @include mq($from: desktop) {
      display: grid;
      grid-template-columns: 3fr 1fr;
      column-gap: 2rem;
    }
    @include mq($from: 1200px) {
      @include rfs(5rem, column-gap);
    }
  }

  .Header__nav--primary,
  .Nav--top-nav,
  #shopify-section-announcements {
    display: none;
  }
  .Header__nav--mobile {
    visibility: hidden;
  }
}

.table-cart-items {
  width: 100%;
  margin-bottom: 1.5rem;
  font-size: 1rem;

  td,
  th {
    padding: clamp(1.2rem, 3vw, 2rem);
    border: 0;
    vertical-align: top;
  }
  th {
    &:first-child {
      text-align: left;
    }
  }
  thead {
    th {
      color: gc(ui, cream);
    }
  }
}

// ----------------------------------------------------------------------------
// LINE ITEM
// ----------------------------------------------------------------------------
.cart-line-item {
  border-bottom: 1px solid gc(muted);

  @include mq($until: tablet) {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 3fr;
    column-gap: 1rem;
    padding: 0 0 1rem 0;
    margin-bottom: 1rem;
    grid-template-areas:
      "thumbs title"
      "thumbs total"
      "thumbs quantity";
    td {
      padding: 0 !important;
    }
    [data-label="Product"] {
      grid-area: thumbs;
    }
    .product-details {
      grid-area: title;
    }
    [data-label="Total"] {
      grid-area: total;
      font-weight: 700;
      align-self: flex-end;
    }
    [data-label="Quantity"] {
      grid-area: quantity;
      align-self: flex-end;
    }
  }

  .product-details {
    @include mq($from: tablet) {
      padding-left: 1rem;
    }
  }

  [data-label="Price"],
  [data-label="Total"] {
    span {
      white-space: nowrap;
    }
  }

  .product-properties {
    margin-bottom: 1.2rem;
  }

  td {
    padding-top: 0.6rem;
    padding-bottom: 0.6rem;

    &:before {
      font-weight: 600;
      text-transform: uppercase;
      font-size: ms(-1);
      letter-spacing: 0.2em;
    }
    &[data-label="Product"] {
      &:before {
        display: none;
      }
    }
    @include mq($from: 900px) {
      text-align: center;
      padding-top: 2rem;
      padding-bottom: 2rem;
    }
    @include mq($from: desktop) {
      text-align: left;
    }
  }
  &:last-of-type {
    @include mq($from: 900px) {
      td {
        border-bottom: none;
      }
    }
  }
  .thumbnail {
    display: block;
    position: relative;
    min-width: 7rem;
    @include mq($from: tablet) {
      min-width: 10rem;
    }
    width: 100%;
    img {
      @include absolute(top 0 right 0 bottom 0 left 0);
      width: 100%;
      height: 100%;
      @include object-fit(contain, center);
    }
  }
  input[type="number"] {
    width: 100%;
    text-align: center;
    max-width: 5rem;
  }
  p {
    margin: 0 0 0.75em 0;
  }

  .price {
    &.line-price {
      font-size: 1.2em;
    }
  }
  .product-title {
    display: block;
    font-size: 0.9em;
    text-transform: uppercase;
    font-weight: 400;
    padding-right: 1.2em;
    @include mq($from: tablet) {
      padding-right: 0;
    }
  }
  .vendor {
    display: block;
    font-size: 0.8em;
    font-style: italic;
  }
  .variant_title {
    font-size: 0.8em;
    margin-top: 0.5rem;
    @include mq($from: tablet) {
      @include rfs(4rem, margin-top);
    }
  }
  td[data-label="Product"] {
    padding-left: 0;
    padding-right: 1rem;
  }
  [data-label="Price"],
  [data-label="Quantity"],
  [data-label="Total"] {
    .label {
      display: block;
      letter-spacing: unset;
      text-transform: none;
      font-weight: 400;
      margin-bottom: 1em;
    }
  }
  .quantity-select {
    display: flex;
    justify-content: flex-end;
    .Button,
    input {
      border: 2px solid var(--color-ui-secondary);
      padding: 0.5em;
      height: 2em;
    }
    input {
      margin: 0 0.5em;
      max-width: 3rem;
      text-align: center;
      line-height: 1;
    }
  }
  [data-label="Total"] {
    text-align: right;
    padding-right: 0;
    @include mq($from: tablet) {
      position: relative;
    }
  }
  .Button--remove {
    position: absolute;
    bottom: unset;
    top: 0;

    right: 0;
    letter-spacing: unset;
    text-transform: none;
    font-style: italic;
    color: var(--color-ui-secondary);
    @include mq($from: tablet) {
      top: unset;
      bottom: 1rem;
    }
  }
}

.cart-item-properties {
  $spacing: 1em;
  font-size: ms(-2);
  display: flex;
  flex-flow: row wrap;
  text-align: left;
  width: calc(100% + #{$spacing * 2});
  margin-left: -$spacing;
  margin-right: -$spacing;
  div {
    // margin-right: 1em;
    margin: $spacing;
    padding-right: 1em;
    &:not(:last-child) {
      border-right: 1px solid gc(muted, base3);
    }
    &:not(:first-child) {
      margin-left: 1em;
    }
  }
  dt {
    text-transform: uppercase;
    font-weight: 600;
    font-size: ms(-3);
  }
  dd {
    margin: 0;
  }
}

.special_instructions {
  @include mq($from: tablet) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 1rem;
  }
  .GiftWrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background-color: #fafafa;
    padding: 1.5rem;
    min-height: 6rem;
    .row {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      margin-bottom: 0.5em;
    }
    input {
      flex-grow: 1;
    }
    label {
      text-transform: none;
      letter-spacing: unset;
      font-weight: 200;
      margin: 0 0.5em;
    }
    .Button {
      width: 100%;
      transition: all 300ms ease-out;
      margin-top: 0.5em;
      &:disabled {
        visibility: hidden;
        opacity: 0;
        transform: scale(0);
      }
    }
  }
}

.cart-notes {
  label,
  textarea {
    width: 100%;
    height: 100%;
  }
  label {
    display: block;
    text-align: center;
  }
  ::placeholder {
    color: var(--color-text-muted);
  }
}

.cart-totals {
  .subtotal__wrapper {
    border-bottom: 1px solid rgba(63, 63, 63, 0.1);
  }
  .subtotal {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.5em;
    //padding-bottom: 1em;

    span.label {
      font-weight: 400;
      text-transform: none;
      letter-spacing: unset;
    }

    &:first-child {
      span {
        font-weight: 700;
      }
    }
    &.shipping_message {
      margin-bottom: 1em;
      padding-top: 1em;
      padding-bottom: 1em;
    }
  }

  .shipping-note {
    font-style: italic;
    color: gc(text, neutral);
  }
}
.route-widget {
  justify-content: center !important;
}
.route-disclaimer {
  margin-bottom: 1em;
  text-align: center;
  p {
    margin-bottom: 0;
    font-size: 0.7rem;
  }
}
.cart-totals--mobile {
  margin-bottom: 2rem;
  .subtotal {
    border-bottom: 0;
    padding-bottom: 0;
  }
  .Button {
    width: 100%;
  }
}

.cart-navigation {
  flex-direction: column;

  .Nav__item,
  .Button {
    width: 100%;
  }

  .Nav__item {
    padding: 0;
    margin: 0.2em 0;
    width: 100%;
    input {
      width: 100%;
    }
    .Button {
      padding: 1em;
    }
  }
  .quick_checkout {
    margin-top: 1em;
  }
  .additional-checkout-buttons {
    ul {
      flex-wrap: wrap !important;
      flex-direction: row !important;
    }

    li {
      flex-basis: 50% !important;
      &:nth-child(odd) {
        padding-right: 5px !important;
      }
    }
  }
}

.empty-cart {
  text-align: center;
  font-size: 1.2rem;
  padding-top: 8vh;
  padding-bottom: 4vh;
}
