.Accordion__container {
  margin-left: auto;
  margin-right: auto;
}

.Accordion__toggle {
  cursor: pointer;
  padding: 1.4em 0;
  display: flex;
  align-items: center;
  .Accordion__container {
    text-align: center;
  }
  h6 {
    margin: 0;
    color: inherit;
    text-transform: none;
    font-family: $f-sans;
    @include rfs(2rem);
  }
  .icon {
    margin-left: auto;
    font-size: 1.5em;
    &.minus{
      display: none;
    }
  }
}

.Accordion__item {
  border-top-width: 1px;
  border-top-style: solid;
  border-top-color: var(--color-ui-muted);
  &:first-child{
    border-top-color: var(--color-ui-secondary);
  }
  &:last-child{
    border-bottom: 1px solid var(--color-ui-secondary);
  }
  
  .contentWrapper {
    /* @include rfs(3rem,padding-left);
    @include rfs(3rem,padding-right); */
    
    max-height: 0;
    overflow: hidden;
  }
  &.is-active {
    .Accordion__toggle {
      //outline: none;
    }
    .contentWrapper {
      max-height: none;
      padding-top: 1em;
      padding-bottom: 3em;
    }
    .minus{
      display: inline-block;
    }
    .plus{
      display: none;
    }
  }
}