.Loop {
  @include rfs(3rem, margin-top);
  @include rfs(3rem, margin-bottom);
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(19rem, 1fr));
  @include rfs(3rem, column-gap);
  @include rfs(5rem, row-gap);
  &[data-layout*='dense'] {
    grid-template-columns: repeat(auto-fill, minmax(18rem, 1fr));
  }
  &[data-layout*='spacious'] {
    @include mq($from: desktop) {
      grid-template-columns: repeat(auto-fill, minmax(26rem, 1fr));
    }
  }
  .Card,
  .Tile {
    margin-top: 0;
    margin-bottom: 0;
  }
}

// ----------------------------------------------------------------------------
// LOOP PRODUCTS
// ----------------------------------------------------------------------------

.Loop--products{
  grid-template-columns: 1fr;
  @include mq($from: tablet){
    grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
  }
  &[data-layout='dense']{
    grid-template-columns: 1fr 1fr;
    @include mq($from: tablet){
      grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
    }
  }
  column-gap: 0.5rem;
  @include mq($from: tablet){
    column-gap: 1rem;  
  }
  row-gap: 3rem;
  margin-top: 2rem;
  
}

// ----------------------------------------------------------------------------
// LOOP STAFF
// ----------------------------------------------------------------------------

.Loop--staff{
  margin: 0;
  gap: 0;
  grid-template-columns: 1fr 1fr;
  @include mq($from: desktop){
    grid-template-columns: 1fr 1fr 1fr;
  }
}


// ----------------------------------------------------------------------------
// LOOP FAQS
// ----------------------------------------------------------------------------
.Loop--faqs {
  display: block;
  .Entry {
    border-bottom: 1px solid gc(muted);
    padding-top: 1em;
    padding-bottom: 0.4em;
  }
  .Entry__title {
    font-family: $f-sans;
    font-size: 1.1rem;
    letter-spacing: 0.2em;
    padding: 0.2em 0.2em;
  }
}
// ----------------------------------------------------------------------------
// LOOP PRODUCT HIGHLIGHTS
// ----------------------------------------------------------------------------

.Loop--productHighlights{
  grid-template-columns: 1fr;
  margin-top: 0;
  margin-bottom: 0;
  @include mq($from: tablet){
    grid-template-columns: 1fr 1fr;
  }
  gap: 1rem;
}

// ----------------------------------------------------------------------------
// LOOP COLLECTIONS
// ----------------------------------------------------------------------------
.Loop--collections {
  column-gap: 1rem;
}

// ----------------------------------------------------------------------------
// LOOP BLOG
// ----------------------------------------------------------------------------
.Loop--blog {
  grid-template-columns: 1fr;
  column-gap: 2rem;
  @include mq($from: tablet){
    grid-template-columns: repeat(2, 1fr);
    .Card[data-layout*='horizontal']{
      grid-column: 1 / span 2;
    }
  }
  @include mq($from: desktop){
    grid-template-columns: repeat(3, 1fr);
    .Card[data-layout*='horizontal']{
      grid-column: 1 / span 3;
    }
  }
}


// ----------------------------------------------------------------------------
// COVER
// The cover element serves an image and context information for the loop
// that ins being displayed
// ----------------------------------------------------------------------------


.page-load-status {
  text-align: center;
  color: gc(text, neutral);
  @include rfs(1.4rem);
  font-weight: normal;
  display: none;
}

