.Card {
  display: grid;
  @include rfs(3rem, margin-top);
  @include rfs(3rem, margin-bottom);
  grid-template-columns: 1fr;
  grid-template-areas: "media" "content";

  &[data-layout*="horizontal"] {
    gap: 2rem;
    @include mq($from: 900px) {
      &[data-layout*="media-content"] {
        grid-template-areas: "media content";
        grid-template-columns: 1fr 1.4fr;
        &[data-layout*="60-40"] {
          grid-template-columns: 1.5fr 1fr;
        }
      }
      &[data-layout*="content-media"] {
        grid-template-areas: "content media";
        grid-template-columns: 1.4fr 1fr;
        &[data-layout*="60-40"] {
          grid-template-columns: 1fr 1.5fr;
        }
      }
    }
  }

  &[data-layout*="vertical"] {
    grid-template-columns: 1fr;
    row-gap: 2rem;
    &[data-layout*="media-content"] {
      grid-template-areas: "media" "content";
      grid-template-rows: auto minmax(auto, 1fr);
    }
    &[data-layout*="content-media"] {
      grid-template-areas: "content" "media";
      grid-template-rows: minmax(auto, 1fr) auto;
    }
  }

  &.image-fill {
    .Card__media {
      img {
        @include absolute(top 0 right 0 bottom 0 left 0);
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  .Buttons {
    margin-top: auto;
    .Button {
      flex-grow: 1;
    }
  }
}
.Card__media {
  margin: 0;
  grid-area: media;
  position: relative;
  &:before {
    content: "";
    display: block;
    width: 100%;
    padding-top: 95%;
  }
  img {
    @include absolute(top 0 right 0 bottom 0 left 0);
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &[data-layout*="image-contain"] {
    img {
      object-fit: contain;
    }
  }
}

.Card__details {
  text-align: center;
}

// ----------------------------------------------------------------------------
// PRODUCT CARD
// ----------------------------------------------------------------------------
.Card.Card--product {
  position: relative;
  margin-top: 0;
  margin-bottom: 0;
  &:hover {
    .animate-images {
      .mainImg {
        opacity: 0;
      }
    }
  }
  .Card__media {
    background: white;
    &:before {
      padding-top: 150%;
    }
    img {
      object-fit: contain;
    }
    .mainImg {
      transition: opacity 300ms ease-out;
    }
  }
  .Card__details {
    [data-price-wrapper] {
      font-size: 0.8rem;
      color: var(--color-text-muted);
    }
  }
  .Card__title {
    line-height: 1em;
    font-family: $f-sans;
    font-size: 0.8rem;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    a {
      font-weight: 300;
    }
  }
  .Badge {
    position: absolute;
    top: 0.5rem;
    left: 0.5rem;
    font-size: 2rem;
    @include mq($from: tablet) {
      top: 1rem;
      left: 1rem;
      font-size: 3.1rem;
    }
  }
  .swym-button {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    width: 2rem;
    height: 2rem;
    @include mq($from: tablet) {
      font-size: 1.6rem;
      top: 1rem;
      right: 1rem;
      width: 50px;
      height: 50px;
    }
    &.disabled:hover {
      opacity: 0.7;
    }
  }
  &.gift-card {
    .Card__media:before {
      padding-top: 60%;
    }
  }
}

// ----------------------------------------------------------------------------
// POST CARD
// ----------------------------------------------------------------------------

.Card--article {
  .Card__media {
    &:before {
      padding-top: 90%;
    }
  }
  .Card__details {
    text-align: left;
    padding: 0 1rem;
    @include mq($from: tablet) {
      padding: 0 2rem;
    }
    .Article__title {
      text-transform: uppercase;
      text-align: center;
      font-size: 1rem;
      font-family: $f-body;
      margin-bottom: 1.5em;
      letter-spacing: 0.15em;
      a {
        font-weight: 200;
      }
    }
    .Article__excerpt {
      margin-bottom: 1em;
    }
    .Button {
      font-weight: 200;
      font-family: $f-body;
      font-size: 0.7rem;
    }
  }
  &[data-layout*="horizontal"] {
    .Article__title {
      @include mq($from: tablet) {
        text-align: left;
      }
    }
    .Card__details {
      align-self: center;
      @include mq($from: tablet) {
        @include rfs(5rem, padding);
      }
    }
    .Card__media:before {
      @include mq($from: tablet) {
        padding-top: 65%;
      }
    }
  }
}

// ----------------------------------------------------------------------------
// POST CARD
// ----------------------------------------------------------------------------

.Card.Card--team-member {
  @include rfs(4rem, row-gap);
  .Card__media {
    max-width: 15rem;
    width: 100%;
    margin: 0 auto;
    border-radius: 50%;
    overflow: hidden;
    background-color: rgb(206, 206, 192, 0.5);
    background-image: url("~Source/img/team-member-bg.png");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    padding: 0.65rem;
    &:before {
      content: unset;
    }

    .Media__content {
      border-radius: 50%;
      overflow: hidden;
      &:before {
        padding-top: 100%;
      }
    }
  }
  h6 {
    font-family: $f-sans;
    text-transform: uppercase;
    font-weight: 200;
    font-size: 1.1rem;

    letter-spacing: 0.2em;
  }
  .job_title {
    display: block;
    font-size: 0.9rem;
  }
  .bio {
    margin-top: 2rem;
    text-align: left;
  }
}

// ----------------------------------------------------------------------------
// OTHER
// ----------------------------------------------------------------------------

.Card--collection {
  text-align: center;
  .Card__media:before {
    padding-top: 150%;
  }
  .Card__title {
    margin: 0;
    font-family: $f-sans;
    text-transform: uppercase;
    font-size: 1.5rem;
    a {
      font-weight: 200;
    }
  }
}

.Card--careers {
  .Card__details {
    text-align: left;
  }
  .Card__title {
    @include rfs(2.5rem);
    font-family: $f-serif;
    text-transform: uppercase;
    line-height: 1;
    margin-bottom: 1em;
  }
  .Button {
    font-size: 0.7rem;
    font-weight: 300;
  }
}
