body#behind-the-brand{
  .Main__header{
    padding: 0;
    .bg{
      width: 100%;
      position: absolute;
      z-index: -1;
      background-position: top left;
      height: 80%;
      top: 50%;
      transform: translateY(-50%);
      @include mq($until: tablet){
        background-size: 100% !important;
      }
    }

  }
  .Hero--brand{
    margin-top: 2rem;
    @include rfs(4rem, margin-bottom);
    .Media__content{ 
      &:before{
        padding-top: 56%;
      }
      
      .Button--play{
        position: absolute;
        top: 60%;
        left: 50%;
        transform: translateX(-50%);
        width: 1em;
        height: 1em;
        @include rfs(11.5rem);
        z-index: 1;
        color: #fff;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.85);  
        @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) { 
          -webkit-backdrop-filter: blur(17px);
          backdrop-filter: blur(17px);
          background-color: rgba(255, 255, 255, 0.25);  
        }
        .icon{
          font-size: 0.25em;
        }
      }
    }
  }
  .Main__content{
    .Container--narrowest{
      @include mq($from: tablet){
        max-width: 45rem;
      }
      @include rfs(5rem, margin-bottom);
    }
    h1{
      text-align: center;
      margin-bottom: 1em;
    }
    .intro{
      line-height: 2;
      @include mq($from: tablet){
        font-size: 1.3rem;
      }
    }
  }
  .Modal__content{
    .Media__content:before{
      padding-top: 56.25%;
    }
  }

  .Section--the-team{
    .Media__content {
      margin-bottom: 2rem;
      &:before{
        padding-top: 56%;
      }
    }
  }
  .Section--grid{
    .Section__content{
      display: grid;
      grid-template-columns: 1fr;
      row-gap: 2rem;
      @include mq($from: tablet){
        display: grid;
        grid-template-columns: 1fr 1fr;
        @include rfs(4rem, column-gap);
      }
      .intro{
        align-self: center;
      }
    }

    .Media__content{
      &:before{
        padding-top: 100%;
      }
      img{
        object-fit: contain;
      }
    }
  }
}