// ----------------------------------------------------------------------------
// PRODUCT TEMPLATE
// ----------------------------------------------------------------------------
.Product {
  @include mq($until: tablet) {
    .Main__content {
      max-width: 100%;
    }
    .Product__main {
      order: 2;
    }
    .Product__details--mobile {
      order: 1;
    }
    .ProductGallery,
    .Product__details,
    .Product__meta--mobile {
      padding-left: 1rem;
      padding-right: 1rem;
    }
  }
  .Main__content {
    display: flex;
    flex-direction: column;
  }
  .Product__main {
    order: 2;
  }
  .Product__details--mobile {
    order: 1;
  }

  .Main__content {
    @include mq($from: desktop) {
      display: block;
      position: relative;
    }
  }

  .swym-wishlist-button-bar {
    z-index: 1;
    position: absolute;
    @include mq($from: desktop) {
      @include rfs(3rem, top);
      right: 0;
    }
    .swym-button.disabled {
      opacity: 1;
    }
  }

  .product-variant-stock {
    text-align: center;
    font-size: 0.9rem;
    @include mq($from: tablet) {
      font-size: 1.15rem;
    }
  }
  #KiwiSizingChart {
    text-align: center;
  }
  .kiwisizing-clones {
    display: none;
    flex-direction: column;
    align-items: center;
    @include mq($from: desktop) {
      align-items: flex-start;
    }
  }
  [data-size-guide-trigger],
  .sizing-chart-modal-link.with-icon {
    color: var(--color-ui-secondary);
    padding-left: 0 !important;
    text-decoration: underline;
    text-transform: uppercase !important;
    font-size: 0.8rem;
    font-family: $f-sans2;
    letter-spacing: 0.15em;
    svg {
      display: none;
    }
  }
  [data-size-guide-trigger],
  [data-size-calc-trigger] {
    margin-bottom: 1em;
  }
  [data-size-calc-trigger] {
    text-transform: none;
    font-weight: 200;
    letter-spacing: unset;
    border: 1px solid #9b9b9b;
    padding: 0 6px;
  }
  .sizing-calculator-modal-button {
    display: none;
  }
  .loox-rating {
    font-size: 1.5rem;
    font-weight: 200;
    span {
      font-size: 1rem;
    }
  }
}

.Product__main {
  display: grid;
  position: relative;
  @include mq($from: desktop) {
    grid-template-columns: 1fr 0.4fr;
    column-gap: 3rem;
  }
  @include mq($from: 1200px) {
    @include rfs(8rem, column-gap);
  }
}

.Product__form--wrapper {
  margin-bottom: 1rem;
  background-color: #fff;
  @include mq($from: desktop) {
    @include rfs(3rem, padding-top);
  }
  &.sticky {
    position: fixed;
    top: 0;
    z-index: 1;
  }
}

@import "../../base/blocks/product/ProductGallery.scss";
@import "../../base/blocks/product/ProductDetails.scss";

.loox-reviews-default {
  max-width: 1440px;
}

.Highligths {
  //border-top: 1px solid var(--color-ui-primary);
  @include rfs(3rem, margin-bottom);
  padding-top: 1rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  @include mq($from: desktop) {
    display: block;
  }

  .Highlight {
    width: calc(100% / 3);
    text-align: center;
    @include mq($from: desktop) {
      width: 100%;
      display: grid;
      grid-template-columns: 1.5rem 1fr;
      column-gap: 1.5rem;
      align-content: center;
      &:not(:last-child) {
        margin-bottom: 1rem;
      }
    }
  }
  .icon {
    width: 100%;
    height: auto;
    max-width: 2.5rem;
  }
  .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 0.9rem;
    font-weight: 300;
    text-align: center;
    margin-top: 1rem;
    padding: 0 0.5em;
    @include mq($from: desktop) {
      text-transform: uppercase;
      letter-spacing: 0.2em;
      text-align: left;
      padding: 0;
      margin-top: 0;
    }
  }
  p:last-child {
    margin: 0;
  }
}

.Product__details--mobile {
  margin-bottom: 1rem;
  .wrapper {
    max-width: calc(100% - 2rem);
    margin: 0 auto;
    text-align: center;
    padding-top: 1rem;
    display: flex;
    flex-direction: column;
    > * {
      order: 4;
    }
    h1 {
      order: 1;
    }
    [data-price-wrapper] {
      order: 2;
    }
    .yotpo-wrapper {
      order: 3;
    }
  }
  .Product__title {
    padding: 0 2rem;
  }
  [data-price-wrapper] {
    display: flex;
    justify-content: center;
    width: 100%;
    @include rfs(2rem);
    font-family: $f-sans2;
    letter-spacing: 0.05em;
    margin-bottom: 0.1em;
    [data-compare-price] {
      display: inline-block;
      font-size: 0.8em;
      text-decoration: line-through;
      margin-right: 1rem;
    }
  }
}

.StickyProduct__form--wrapper,
.MobileProduct__form--wrapper {
  background-color: #fff;
  padding: 0.5rem 1rem;
  width: 100%;
  @include mq($from: tablet) {
    padding: 0.5rem 0;
  }
  &.sticky {
    position: fixed;
    top: 0;
    z-index: 2;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.05);
  }
}
.StickyProduct__form,
.MobileProduct__form {
  .inner {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 0.5rem;
    row-gap: 0.5rem;
    margin-bottom: 0.5em;
    &.single-variant,
    &.coming-soon,
    &.unavailable {
      .Buttons {
        grid-column: 1 / span 2;
      }
    }
  }
  #anchor-for-bis-mobile {
    grid-column: 1 / span 2;
  }
  [data-price-wrapper] {
    text-align: center;
    @include rfs(2rem);
    font-family: $f-sans2;
    letter-spacing: 0.05em;
  }
  .Product__select [class*="control"] {
    min-height: 3.7em;
    padding: 0 0.5em;
  }

  [class*="IndicatorsContainer"] {
    .icon {
      font-size: 1rem;
    }
  }
  .Product__select {
    font-size: 0.8rem;
  }
  .Product__select [class*="ValueContainer"],
  .Button {
    font-size: 0.8rem;
  }
  .Button {
    padding: 0 1em;
    height: 100%;
    width: 100%;
  }
}

.StickyProduct__form--wrapper {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  padding: 1rem 1rem 0.5rem;
  z-index: 1;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
}
.StickyProduct__form {
  &.Container--narrowest {
    max-width: 30rem;
  }
  @include mq($from: tablet) {
    .inner {
      width: 100%;
    }
  }
}

.product-baby-mini-variant {
  .product-baby-variant,
  .product-mini-variant {
    display: grid;
    grid-template-columns: 6rem 1fr;
    background-color: #f6f4f2;
    margin-bottom: 1rem;
  }
  .info {
    padding: 1rem;
  }
  .Media__content:before {
    padding-top: 100%;
  }
  span {
    font-size: 1.25rem;
    font-style: italic;
    display: block;
  }
  .Button {
    text-transform: none;
  }
}

afterpay-placement {
  margin-top: 0;
  text-align: center;
  @include mq($from: desktop) {
    text-align: left;
  }
}

.ModelsStats {
  .Entry {
    display: grid;
    gap: 1rem;
    grid-template-columns: 1fr 4rem;
    background-color: #f6f4f2;
    padding: 1rem 2rem 0 1rem;
    border-radius: 0.7em;
    margin-bottom: 1rem;
    align-items: center;
  }
  .Entry__figure {
    border-radius: 5rem;
    overflow: hidden;
    &:before {
      padding-top: 100%;
    }
    img {
      object-position: top center;
    }
  }
  .Entry__details {
    margin-bottom: 1rem;
  }
}

.Section--productAccordions {
  margin: 1rem 0;
}
