.Section--featured-articles {
  .Slider {
    display: grid;
    grid-template-columns: 1fr;
    @include mq($from: tablet) {
      grid-template-columns: repeat(auto-fill, minmax(26rem, 1fr));
    }
    @include rfs(3rem, column-gap);
    @include rfs(5rem, row-gap);
    &.flickity-enabled {
      display: block;
      $spacing: 0.5rem;
      margin-left: -$spacing;
      margin-right: -$spacing;
      .Card {
        width: 100%;
        @include mq($from: tablet) {
          width: calc(100% / 2);
        }
        @include mq($from: desktop) {
          width: calc(100% / 3);
        }
        padding: $spacing;
      }
    }
  }
  .flickity-button {
    @include mq($until: tablet) {
      display: none;
    }
  }
  .flickity-page-dots {
    display: none;
  }
  .SliderNav {
    display: flex;
    justify-content: center;
    @include mq($from: tablet) {
      display: none;
    }
    .button--previous,
    .button--next {
      max-width: 5rem;
      top: 60%;
      margin: 0 1em;

      @include mq($until: tablet) {
        width: 50%;
      }

      svg {
        color: black;
      }
    }
  }
}
