.Hero {
  position: relative;
  
  .Hero__figure {
    position: relative;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    &:before {
      display: block;
      content: "";
      width: 100%;
      padding-top: 130%;
      
      @include mq($from: tablet) {
        padding-top: 80%;
      }
      @include mq($from: desktop) {
        padding-top: 43%;
      }
    }
    &:after{
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0,0,0,0.15);
    }
    @include mq($until: desktop) {
      &.Container {
        max-width: 100%;
      }
    }
  }
  .Hero__content {
    position: absolute;
    padding-bottom: 1rem;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    .inner{
      width: 100%;
      margin-left: auto;
      margin-right: auto;

    }
    @include mq($from: desktop){
      flex-direction: row;
      .inner{
        max-width: 800px;
        transform: translateY(20%);
      }
    }
    h1 {
      font-size: 6rem;
      font-weight: 800;
      color: white;
      margin-bottom: 0px;
      text-align: center;
      @include mq($until: tablet) {
        font-size: 4.2rem;
      }
    }
    .Buttons {
      display: flex;
      justify-content: center;
    }
    .Button {
      
    }
    .intro{
      text-align: center;
      color: white;
      text-transform: uppercase;
      font-weight: bold;
      margin-top: 1.5em;
      p{
        font-family: $f-serif;
        font-size: 1.6rem;
        margin-bottom: 0.5em;
      }
      @include mq($from:desktop){
        position: absolute;
        max-width: 10em;
        line-height: 0.8;
        text-align: left;
        right: 1em;
        bottom: 1em;
        
        p{ 
          font-size: 2rem;
          line-height: 1;
        }
      }
      @include mq($from:1400px){
        @include rfs(10em,right);
        @include rfs(5em,bottom);
      }
    }
  }
}


.Hero--page,
.Hero--article{
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  @include rfs(5rem,margin-bottom);
  h1{
    background-color: transparent;
    color: white;
    text-shadow:rgba(23,23,23,.51) 0 0 12px;
    @include rfs(5.5rem);
  }
  .inner{
    padding: 1rem;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    position: relative;
    z-index: 1;
  }
  &:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.15);  
  }
  @include mq($from: desktop){
    &:before{
      content: "";
      position: absolute;
      width: 100vw;
      height: 50px;
      top: 0px;
      left: 50%;
      margin-left: -50vw;
      background: v(color-ui-red);
      z-index: -1;
    }
  }
  @include mq($until: desktop){
    &.Container{
      max-width: unset;
    }
  }
}

.Hero--page{
  padding-top: 80%;
  @include mq($from: tablet){
    padding-top: 40%;
  }
  
  
  .inner{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

.Hero--article{
  @include mq($from: tablet){
    padding-top: 40%;
  }
  .inner{
    @include mq($from: tablet){
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}


.Hero--careers{
  padding: 1rem;
  @include mq($from: tablet){
    padding: 2rem;
  }
  @include rfs(5rem, margin-bottom);
  position: relative;
  @include mq($until: tablet){
    max-width: 100%;
  }
  .bg{
    width: 100%;
    position: absolute;
    z-index: -1;
    background-position: top left;
    padding-top: 45%;
    top: 0;
    left: 0;
    @include mq($until: tablet){
      background-size: 100% !important;
    }
  }
  .Hero__media:before{
    padding-top: 70%;
    @include mq($from: tablet){
      padding-top: 56%;
    }
  }

  .Hero__content {
    align-items: flex-end;
    position: relative;
    height: unset;
    padding-bottom: 0;
    
    .inner{
      background-color: var(--color-ui-primary);
      padding: 1rem 2rem;
      font-size: 1.2rem;
      max-width: unset;
      position: relative;
      transform: translateY(0);
      margin-top: -1rem;
      @include mq($from: tablet){
        @include rfs(5rem 8.1rem, padding);
        margin-top: -7rem;
        @include rfs(2rem);
      }
      @include mq($from: desktop){
        margin-top: -14rem;
      }
      
    }
  }
}