$f-sans: 'Spectral', sans-serif;
$f-body: 'Spectral', sans-serif;
$f-sans2: 'Arquitecta-Book', sans-serif;
$f-serif: 'Fiona', sans-serif;
$f-display: 'Wild Love Script', sans-serif;
$f-stack-default: $f-sans;

:root {
  --serif: 'Times New Roman', serif;
  --sans: Helvetica, Verdana, Arial, sans-serif;
}

$base-font-size: 1rem;

// MODULAR SCALE VARS
$ms-base: $base-font-size;
// $ms-base          : 14px;
// $ms-ratio         : $golden;
// $ms-ratio: $major-second;
$ms-ratio: 1.138;

$f-size-h1: 6rem;
$f-size-h2: 5.5rem;
$f-size-h3: 4.3rem;
$f-size-h4: 3.5rem;
$f-size-h5: 3rem;
$f-size-h6: 2.5rem;

$f-size-xxs: ms(-3);
$f-size-xs: ms(-2);
$f-size-s: ms(-1);
$f-size: 0.95rem;
$f-size-l: ms(1);
$f-size-xl: ms(3);
$f-size-xxl: ms(5);

$f-line-height-ratio: 1.65;
$f-line-height: $f-size * $f-line-height-ratio !default;
