.Announcement {
  text-align: center;
  background: v(color-ui-primary);
  font-family: $f-sans;
  .Container {
    padding: 0.5em 0;
    font-size: 0.8rem;
    @include mq($from: desktop) {
      font-size: 0.9rem;
    }
    display: flex;
    &.is-slider {
      opacity: 0;
    }
    &.flickity-enabled {
      display: block;
      opacity: 1;
    }

    .Announcement__item {
      width: 100%;
      padding: 0.1em 0.5em;
      @include mq($from: tablet) {
        padding: 0.3em 1em;
      }
    }
    a {
      font-weight: 200;
    }
  }
  p {
    margin-bottom: 0;
  }
}

.Header {
  z-index: 1;
  padding: 1rem 0;
  position: relative;
  > .Container {
    display: grid;
    column-gap: 0.5rem;
    row-gap: 1rem;
    grid-template-columns: min-content 1fr min-content;
    grid-template-areas: "mobilenav brand secnav";
    @include mq($from: tablet) {
      column-gap: 1.4rem;
    }

    @include mq($from: desktop) {
      grid-template-columns: 0.1fr 1fr 0.1fr;
      grid-template-areas: "brand brand brand" ". mainnav secnav";
      row-gap: 0;
    }

    @include mq($from: 1200px) {
      @include rfs(4rem, column-gap);
    }
  }

  .icon--primary {
    @include rfs(1.7rem);
    margin: 0 0.4em;
  }
}

.Header__brand {
  grid-area: brand;
  svg {
    width: 100%;
    max-width: 22rem;
    display: block;
    margin: 0 auto;
  }
}
.Header__nav--mobile {
  grid-area: mobilenav;
  display: flex;
  align-items: center;
  width: 10%;
  @include mq($from: desktop) {
    display: none;
  }
  .mobile-toggler {
    color: inherit;
  }
}
.Header__nav--primary {
  grid-area: mainnav;

  @include mq($until: desktop) {
    display: none;
  }
  .Nav {
    justify-content: center;
  }
  .Nav__item--main {
    padding-top: 1rem;
    padding-bottom: 1.25rem;
    text-align: center;
    @include mq($from: 1300px) {
      @include rfs(1.6rem, padding-left);
      @include rfs(1.6rem, padding-right);
    }
    > a {
      text-transform: uppercase;
    }
  }
}
.Header__nav--secondary {
  grid-area: secnav;
  display: flex;
  justify-items: flex-end;
  @include mq($until: desktop) {
    padding-bottom: 0px;
    //width: 40%;
  }
  .Nav--utilities {
    @include mq($from: desktop) {
      padding-top: 1rem;
      padding-bottom: 1.25rem;
    }

    .Nav__item {
      font-size: 0.8rem;
    }
  }
  .Nav {
    justify-content: flex-end;
  }
  .Nav--main {
    @include mq($from: desktop) {
      display: none;
    }
  }
}

.Nav--headerMobile {
  background: v(color-ui-red);
  color: white;
  padding: 3rem 1rem 1rem;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  .icon--primary {
    width: 3em;
    height: 3em;
    padding: 10px;
    border-radius: 50px;
    background: white;
    color: black;
    margin-bottom: 20px;
    cursor: pointer;
  }
  form {
    display: flex;
    width: 100%;
    border-bottom: 1px solid var(--color-ui-muted);
    input {
      width: 100%;
      border: 0;
      text-align: center;
    }
    ::-webkit-input-placeholder {
      /* Chrome/Opera/Safari */
      text-transform: uppercase;
      letter-spacing: 0.15em;
    }
    ::-moz-placeholder {
      /* Firefox 19+ */
      text-transform: uppercase;
      letter-spacing: 0.15em;
    }
    :-ms-input-placeholder {
      /* IE 10+ */
      text-transform: uppercase;
      letter-spacing: 0.15em;
    }
    :-moz-placeholder {
      /* Firefox 18- */
      text-transform: uppercase;
      letter-spacing: 0.15em;
    }
  }
}
.mobileNav {
  .Drawer__inner {
    padding: 0px;
    background-color: white;
    width: 100%;
    overflow: auto;
  }
  .Button--close {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    line-height: 1;
    padding: 0;
    width: 2.5rem;
    height: 2.5rem;
    background: var(--color-ui-secondary);
    color: white;
    min-height: unset;
    font-size: 1.5rem !important;
    font-weight: 300;
    border-radius: 50%;
    border: none;
  }
  .Nav__item:not(.Media) + .Media {
    margin-top: 2rem;
  }
  .Media {
    width: 100%;
  }
  .Media__content {
    width: 100%;
    &:before {
      padding-top: 130%;
      background-color: rgba(156, 131, 111, 0.25);
      position: relative;
      z-index: 1;
    }
    figcaption {
      font-family: $f-serif;
      position: absolute;
      width: 100%;
      padding: 1rem;
      color: #fff;
      top: 50%;
      transform: translateY(-50%);
      text-align: center;
      font-size: 4.5rem;
      line-height: 0.8;
      font-weight: 200;
      z-index: 2;
    }
  }
  .Nav__item {
    margin-bottom: 1rem;
    text-transform: uppercase;
    letter-spacing: 0.15em;
    font-weight: 400;
    .Nav__link {
      font-weight: inherit;
    }
  }
  .Nav--mainMobile {
  }
  .Nav--secMobile {
    border-top: 1px solid var(--color-ui-muted);
    margin-top: 2rem;
    padding-top: 2rem;
  }
}

.Search {
  background-color: v(color-ui-primary);
  padding: 0 1rem 1rem 1rem;
  display: none;
  .Search__form {
    max-width: 40rem;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    border-bottom: 1px solid v(color-ui-secondary);
  }
  input {
    border: 0;
    width: 100%;
    background-color: unset;
  }
}
#fsb_background {
  z-index: 1 !important;
}
