form {
  margin: 0;
}

a,
area,
button,
[role='button'],
input,
label,
select,
summary,
textarea {
  touch-action: manipulation;
}

button,
input[type='submit'],
label[for] {
  cursor: pointer;
}

textarea {
  min-height: 100px;
}

select::-ms-expand {
  display: none;
}

input,
input[type='text'],
input[type='email'],
input[type='url'],
input[type='password'],
input[type='search'],
select,
textarea {
  border: mg($forms, input, border);
  border-radius: mg($forms, input, radius);
  background: mg($forms, input, background);
  padding: mg($forms, input, padding);
  outline: none;
  font-weight: 400;
  font-family: $f-body;
  font-size: mg($forms, input, font-size);
  color: mg($forms, input, color);
  text-transform: none;
}

input[type='text'],
input[type='search'],
input[type='number'],
input[type='password'],
input[type='email'],
input[type='tel'],
input[type='url'] {
  height: mg($forms, input, height);
  line-height: mg($forms, input, height);
}

input[type='submit'],
input[type='checkbox'] {
  width: auto;
}

input[type='file'] {
  background: none;
  padding: 0;
  border: none;
}

textarea {
  padding: 1em 1.25em;
  width: 100%;
}

select {
  display: block;
  -webkit-appearance: none;
  height: mg($forms, input, height);
  line-height: mg($forms, input, height);
  width: 100%;
}
.dropdownIcon {
  @include absolute(bottom mg($forms, input, height) / 2 right 1rem);
  transform: translateY(50%);
  pointer-events: none;
}

label,
.label {
  display: block;
  font-family: $f-sans;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.2em;
  font-size: ms(-2);
}

form {
  .errors {
    text-align: center;
    margin-bottom: 3rem;
    color: gc(text, negative);
    ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }
  }
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: gc(text);
  font-size: 0.75em;
  font-family: $f-body;
}
::-moz-placeholder {
  /* Firefox 19+ */
  color: gc(text);
  font-size: 0.75em;
  font-family: $f-body;
}
:-ms-input-placeholder {
  /* IE 10+ */
  color: gc(text);
  font-size: 0.75em;
  font-family: $f-body;
}
:-moz-placeholder {
  /* Firefox 18- */
  color: gc(text);
  font-size: 0.75em;
  font-family: $f-body;
}

$spacing: 1rem;
.Form__grid {
  display: flex;
  flex-flow: row wrap;
  margin-left: -$spacing / 2;
  margin-right: -$spacing / 2;
  margin-bottom: 1.5rem;

  .Form__input {
    flex-grow: 0;
    flex-basis: calc(100% - #{$spacing});
    margin: $spacing / 2;
    position: relative;
    input,
    textarea {
      width: 100%;
    }
  }

  .Grid-medium-1-2 {
    @include tablet {
      flex-basis: calc(50% - #{$spacing});
    }
  }

  .Grid-medium-1-3 {
    @include tablet {
      flex-basis: calc(33.333% - #{$spacing});
    }
  }

  .Grid-medium-2-3 {
    @include tablet {
      flex-basis: calc(66.666% - #{$spacing});
    }
  }
}

.Form__actions {
  flex-basis: calc(100% - #{$spacing});
  margin: $spacing * 2 $spacing / 2;
  margin-bottom: 4rem;
  display: flex;
  flex-wrap: wrap;
  .push-right {
    margin-left: auto;
  }
  .Button{
    width: 100%;
    @include mq($from: 900px){
      width: unset;
    }
  }
}

#contact_form { 
  .Form__row{
    display: flex;
    column-gap: 1.5rem;
    flex-wrap: wrap;
    @include mq($from: desktop){
      flex-wrap: nowrap;
    }
  }

  .Form__item, 
  input:not(.Button),
  textarea {
    width: 100%;
    font-family: $f-body;
  }
  
}
