.Footer {
  overflow-x: hidden;
  @include rfs(5rem 0, padding);
  .js-backToTop {
    font-size: 3rem;
    width: 1em;
    height: 1em;
    position: fixed;
    z-index: 100;
    bottom: 6rem;
    right: 1.7rem;
    background: var(--color-ui-secondary);
    color: #fff;
    display:flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease-out;
    transform: translateY(20px);
    opacity: 0;
    visibility: hidden;
    &.is-visible {
      transform: translateY(0px);
      opacity: 1;
      visibility: visible;
    }
    span{
      line-height: 0;
      display: block
    }
    .icon{
      font-size: 0.5em;
    }
  }
  .Highlights{
    @include rfs(5rem, margin-bottom);
    padding-top: 2rem;
    background-position: top left;
    @include mq($until: tablet){
      background-size: 100% !important;
    }
  }
  .Highlights__container{
    @include rfs(5rem 0, padding);
    .Container{
      display: flex;
      justify-content: space-around;
    }
  }
  .Entry{
    text-align: center;
    padding: 0.4rem;
    max-width: 20em;
    width: 100%;
    @include mq($from: tablet){
      padding: 1rem; 
    }
    .Media__content{
      margin-bottom: 1.5rem;
      &:before{
        padding-top: 3.5rem;
      }
      img{
        object-fit: contain;
      }
    }
    h6{
      margin-top: auto;
      font-family: $f-sans2;
      font-size: 0.9rem;      
      @include mq($from: tablet){
        text-transform: uppercase;
        letter-spacing: 0.2em;
      }
    }
  }
  .wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;
    @include mq($from: desktop){
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
    }
  }
  .FooterForm{
    margin-bottom: 3rem;
    max-width: 30rem; 
    @include mq($from: desktop){
      max-width: 30%; 
      margin-top: 0;
    }
    .heading{
      color: v(color-ui-secondary);
    }

    //klaviyo form
    form{
      padding: 0 !important;
      >[class*='Column__FormC']{
        >[class*='Row__FormRow']:first-child{
          >[class*='FormComponent']{
            padding-top: 0 !important;
          }
        }
      }
      button{
        height: 38px!important;
        padding: 0 10px !important;
      }
    }
  }
  .Nav--footer{
    width: 100%;
    flex-direction: column;
    align-items: center;
    @include mq($from: tablet){
      flex-direction: row;
      align-items: flex-start;
    }
    @include mq($from: desktop){
      width: 70%;
      max-width: 55rem;
      padding-left: 3rem;
    }
    .Nav__item--level1{
      text-align: center;
      width: 100%;
      @include mq($from: tablet){
        flex-basis: calc(100% / 3);
      }
      @include mq($from: desktop){
        text-align: left;
      }
      .Container{
        max-width: unset;
      }
    }
    .Nav__item.level-2{
      margin-bottom: 0.5em;
    }
  }
}