/**
 * ----------------------------------------------------------------------------
 * BUTTONS
 * 1. Allow us to better style box model properties.
 * 2. Line different sized buttons up a little nicer.
 * 3. Stop buttons wrapping and looking broken.
 * 4. Make buttons inherit font styles.
 * 5. Force all elements using beautons to appear clickable.
 * 6. Normalise box model styles.
 * 7. If the buttonâ€™s text is 1em, and the button is (3 * font-size) tall, then
 *    there is 1em of space above and below that text. We therefore apply 1em
 *    of space to the left and right, as padding, to keep consistent spacing.
 * 8. Previne que o botÃ£o diminua comprima o texto quando dentro de um elemento flexbox
 * ----------------------------------------------------------------------------
 */
// $button_styles: (solid, outline);

%button,
.Button,
.button,
input[type='submit']{
  display: inline-flex; // [1]
  vertical-align: middle; // [2]
  white-space: nowrap; // [3]
  font-family: $f-sans; // [4]
  font-size: 100%; // [4]
  cursor: pointer; // [5]
  border: none; // [6]
  margin: 0; // [6]
  padding-top: 0; // [6]
  padding-bottom: 0; // [6]
  border-radius: mg($forms, input, radius);
  text-transform: uppercase;
  text-align: center;
  transition: all 0.18s ease;
  font-weight: 700;
  text-decoration: none;
  align-items: center;
  justify-content: center;
  letter-spacing: 0.15em;

  svg,
  .icon {
    display: inline-block;
    vertical-align: middle;
    &:first-child {
      margin-right: 0.3em;
    }
    &:last-child {
      margin-left: 0.3em;
    }
    &:only-child {
      margin: 0;
    }
  }

  &:hover {
    text-decoration: none;
  }

  // Disabled state
  &.disabled,
  &[disabled] {
    cursor: not-allowed;
    opacity: 0.4;
  }

  &[data-layout*='block'] {
    width: 100%;
  }

  &.Button--small,
  &[data-layout*='small'] {
    $height: mg($buttons, small, height);
    font-size: mg($buttons, small, font-size);
    line-height: strip-unit($height);
    padding: mg($buttons, small, padding);
    border-radius: mg($buttons, small, radius);
  }

  &[data-layout*='big'] {
    $height: mg($buttons, big, height);
    font-size: mg($buttons, big, font-size);
    min-height: $height;
    line-height: strip-unit($height);
    padding: mg($buttons, big, padding);
    border-radius: mg($buttons, big, radius);
  }
  &[data-layout*='large'] {
    $height: mg($buttons, large, height);
    font-size: mg($buttons, large, font-size);
    min-height: $height;
    line-height: strip-unit($height);
    padding: mg($buttons, large, padding);
    border-radius: mg($buttons, large, radius);
  }

  &[data-style*='text'],
  &[data-layout*='noLayout'] {
    background: none;
    padding: unset;
    min-height: unset;
    line-height: unset;
    padding: 0;
  }

  &[data-style*='solid'] {
    @each $name, $color in map-get($colors, ui) {
      &[data-style*='#{"" + $name}'] {
        color: #fff;
        background-color: $color;
        @if $name == white {
          color: gc(text);
          box-shadow: hsla(199, 28%, 12%, 0.3) 0 0 0.2em, hsla(199, 28%, 12%, 0.1) 0 0 2em;
        }
        @if $name == primary {
          color: gc(text);
        }
        @if $name == gray {
          color: gc(text);
        }
        &:hover{
          background-color: darken($color, 3%);
        }
      }
    }
    &[data-style*='bluetogreen'] {
      background-image: linear-gradient(-45deg, #26c9b0 0%, #2080b9 100%);
    }
  }

  &[data-style*='outline'] {
    @each $name, $color in map-get($colors, ui) {
      &[data-style*='#{"" + $name}'] {
        color: $color;
        background: white;
        box-shadow: $color 0 0 0 1px inset;
      }
    }
    &[data-style*='gradient'] {
      background: -webkit-linear-gradient(0deg, gc(ui, blue), hsla(184, 100%, 36%, 1));
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      border: 1px solid;
      border-image: linear-gradient(to left, #00a8b9 0%, #0080c1 100%);
      border-image-slice: 1;
    }
  }

  &[data-style*='text'],
  &[data-style*='textonly'] {
    @each $name, $color in map-get($colors, ui) {
      &[data-style*='#{"" + $name}'] {
        color: $color;
        background: none;
      }
    }
  }

  &[data-style*='underline'] {
    padding-left: 0;
    padding-right: 0;
    border-bottom: 1px solid;
    min-height: 1em;
    @each $name, $color in map-get($colors, ui) {
      &[data-style*='#{"" + $name}'] {
        color: $color;
        border-color: $color;
      }
    }
  }

  &[data-style*='noStyle'] {
    background: none;
    border: none;
  }

  &[data-style*='icon'] {
    @include rfs(2.4rem);
  }

  [class*='Grid'] & {
    width: 100%;
  }
}

/**
 * ============================================================================
 * BUTTON LAYOUTS
 * ============================================================================
 */

%btn-layout--default {
  $height: mg($buttons, default, height);
  font-size: mg($buttons, default, font-size);
  min-height: $height;
  // line-height: 1em;
  padding: mg($buttons, default, padding);
  border-radius: mg($buttons, default, radius);
  //letter-spacing: 1px;
  &[data-layout*='small'] {
    $height: mg($buttons, small, height);
    font-size: mg($buttons, small, font-size);
    min-height: $height;
    // height        : $height;
    line-height: strip-unit($height);
    padding: mg($buttons, small, padding);
    border-radius: mg($buttons, small, radius);
  }
}

%btn-layout--big,
.Button--big {
  $height: mg($buttons, big, height);
  font-size: mg($buttons, big, font-size);
  // height: $height;
  line-height: strip-unit($height);
  padding: mg($buttons, big, padding);
  border-radius: mg($buttons, big, radius);
}

%btn-layout--text,
.Button--text,
.Button--icon {
  padding-left: 0;
  padding-right: 0;
  min-height: unset;
  line-height: unset;
  background: none;
  color: unset;
  &:hover {
    color: gc(link, hover);
  }
}

.Button--icon {
  @include rfs(1.4rem);
}

/**
 * ============================================================================
 * BUTTON STYLES
 * ============================================================================
 */

.Button--isDisabled {
  &,
  &:hover {
    background-color: gc(muted);
    color: gc(text, muted);
    cursor: not-allowed;
  }
}

/**
* ============================================================================
* BUTTON DECLARATIONS
* Here we extend and assign the styles and layout to each button
* ============================================================================
*/

.Button {
  // @extend %btn-style--default;
  @extend %btn-layout--default;
}
.spr-button,
.shopify-challenge__button{
  @extend %btn-layout--default;
}

.shopify-payment-button__button--unbranded{
  @extend %button;
  @extend %btn-layout--big;
  background-color: black;
  margin-top: 0.5em;
  letter-spacing: 1px;
}