table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.7rem;
  @include mq($from: 900px) {
    font-size: 0.85rem;
  }
  th {
    text-transform: uppercase;
    font-family: $f-sans;
    letter-spacing: 0.15em;
    background-color: var(--color-ui-secondary);
    color: #fff;
    text-align: left;
    padding: 0.8rem;
    font-weight: bold;
    @include mq($from: 900px) {
      font-size: 1.2rem;
    }
  }
  tr, td{
    padding: 0.6rem;
    @include mq($from: 900px) {
      padding: 0.8rem;
    }
  }
  th,td {
    border: 1px solid var(--color-ui-secondary);
  }
}
.responsive-table {
  width: 100%;
}

@include mq($until: 765px) {
  .small--hide {
    display: none !important;
  }
  .responsive-table {
    thead {
      display: none;
    }

    tr {
      display: block;
    }

    // IE9 table layout fixes
    tr,
    td {
      float: left;
      clear: both;
      width: 100%;
    }

    th,
    td {
      display: block;
      margin: 0;
      // padding: $grid-gutter / 2;
    }

    td::before {
      content: attr(data-label);
      float: left;
      padding-right: 10px;
      text-align: center;
    }
  }

  // Add a keyline between rows
  .responsive-table-row + .responsive-table-row,
  tfoot > .responsive-table-row:first-child {
    position: relative;
    margin-top: 10px;
    // padding-top: $grid-gutter / 2;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      // right: $grid-gutter / 2;
      // left: $grid-gutter / 2;
      display: block;
      // border-bottom: 1px solid $color-border;
    }
  }
}

