.template-addresses {
  .Card.address {
    border-bottom: 1px solid gc(muted, lines);
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    display: grid;
    column-gap: 3rem;
    @include mq($from: tablet) {
      grid-template-columns: 1fr 3fr;
    }
    .Card__details {
      padding: 0;
    }
    .Card__options {
      display: inline-flex;
    }
    [data-address-form] {
      @include mq($until: tablet) {
        margin-top: 2rem;
      }
    }
    h4{
      margin-top: 0;
    }
  }
  input[type='checkbox'] + label{
    display: inline;
  }
  label{
    font-size: 0.8em;
  }
}
