.Section--featured-products {
  .Container {
    position: relative;
    &:before,
    &:after {
      content: "";
      position: absolute;
      @include rfs(15rem);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      z-index: -1;
    }
    &:before {
      background-image: url("~Source/img/flower_deco_1.png");
      width: 1em;
      height: 1.5em;
      left: -0.7em;
      bottom: -1.35em;
      transform: rotate(70deg);
      @include mq($from: tablet) {
        width: 0.8em;
        height: 1.2em;
        bottom: 0.15em;
        left: -0.4em;
        transform: unset;
      }
    }
    &:after {
      background-image: url("~Source/img/flower_deco_2.png");
      width: 0.9em;
      height: 0.9em;
      right: -0.1em;
      top: -0.6em;
      transform: rotate(140deg);
      @include mq($from: tablet) {
        top: 0.1em;
        right: -0.45em;
        transform: unset;
      }
    }
  }
  .Loop {
    column-gap: 1rem;
    grid-template-columns: 1fr 1fr;
    @include mq($from: desktop) {
      grid-template-columns: repeat(4, 1fr);
    }
  }
  .Slider {
    $spacing: 0.5rem;
    margin-left: -$spacing;
    margin-right: -$spacing;
    .Card {
      width: 50%;
      @include mq($from: tablet) {
        width: calc(100% / 3);
      }
      @include mq($from: desktop) {
        width: calc(100% / 4);
      }
      padding: $spacing;
    }
  }
}
