// ============================================================================
// NAV
// ============================================================================
.Nav {
  display: flex;
  align-items: center;
  justify-content: center;

  [data-layout*='vertical'] {
    flex-direction: column;
    align-items: flex-start;
    .Nav__item {
      padding-left: 0;
      padding-right: 0;
    }
  }

  [data-style*='icons'] {
    .is-hidden {
      display: none;
    }
    .icon {
      @include rfs(2rem);
      display: block;
    }
  }

  a {
    @include link-colors(gc(text), gc(link, hover));
  }
}

// ============================================================================
// NAV--SECTOR
// ============================================================================
.Nav--sector {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  font-size: 0.85rem;
  letter-spacing: 0.15em;
  .Nav__item {
    font-family: $f-sans;
    text-transform: uppercase;
  }
  a {
    font-weight: 500;
  }
}

// ============================================================================
// NAV-ITEM
// ============================================================================
.Nav__item {
  padding-right: 0.6em;
  padding-left: 0.6em;
}

.Nav__link {
  &.anchor-link {
    cursor: default;
    &:hover {
      color: initial;
    }
  }
}

// ============================================================================
// VARIANTS
// ============================================================================

// MAIN -----------------------------------------------------------------------
.Nav--main {
  //font-size: 0.8rem;
  height: 100%;

  .Nav__item {
    padding: 0;

    &.has-submenu.is-hover {
      .Nav__submenu {
        display: block;
      }
    }

    .Media__content{
      &:before{
        padding-top: 130%;
        background-color: rgba(156, 131, 111, 0.25);
        position: relative;
        z-index: 1;
      }
      figcaption{
        font-family: $f-serif;
        position: absolute;
        width: 100%;
        padding: 1rem;
        color: #fff;
        top: 50%;
        transform: translateY(-50%);
        text-align: center;
        font-size: 3rem;
        line-height: 0.7;
        @include mq($from: 1500px){
          font-size: 4rem;
        }
        z-index: 2;
      }
    }
  }

  .Nav__link {
    font-family: $f-sans;
    font-weight: 400;
    line-height: 1.1;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    color: #626262;
  }

  .Nav__item--main {
    padding-left: 1rem;
    padding-right: 1rem;
    height: 100%;
    display: inline-flex;
    align-items: center;
    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      padding-right: 0;
    }
  }

  .Nav__submenu {
    
    .Nav {
      align-items: flex-start;
    }

    &.level-2 {
      position: absolute;
      left: 0;
      top: 5rem;
      width: 100%;
      background: white;
      z-index: 100;
      display: none;
      overflow-y: hidden;
      >.Nav{
        @include rfs(5rem, padding-top);
        @include rfs(5rem, padding-bottom);
        height: 100%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        @include mq($from: 1150px){
          grid-template-columns: repeat(6, 1fr);
          //grid-template-columns: repeat(auto-fill, minmax(13rem, 1fr));
        }
        column-gap: 0.5rem;
        row-gap: 2rem;
      }
      .simple-item{
        padding: 0 2rem;
        border-left: 1px solid #EFEFEF;
        height: 100%;
        &:first-child{
          padding-left: 0;
          border-left: 0;
        }
      }
      
      .Nav__link{
        white-space: unset;
        font-size: 0.8rem;
        &[href="#"]{
          display: block;
          pointer-events: none;
          font-size: 1.15rem;
          margin-bottom: 1.5em;
        }
      }
    }
    .Nav__item.level-3{
      display: inline-block;
      width: 100%;
    }
  }
}

.Nav--mainMobile {
  text-align: center;
  margin-top: 2rem;
  .Nav__item {
    padding-left: 0;
    padding-right: 0;
    &.level-1 {
      width: 100%;
      background: white;
    }
    &.level-3 {
      margin-bottom: 0.5em;
    }
  }
  .Nav__submenu{
    margin-bottom: 2rem;
  }
  .Nav__link {
    font-family: $f-sans;
    text-transform: uppercase;
    letter-spacing: 0.2em;
    white-space: normal;
    &.level-1 {
      font-size: 1.6rem;
      width: 100%;
      letter-spacing: 0px;
      font-weight: 800;
      display: block;
      margin-bottom: 0.3em;
    }
    &.level-2 {
      font-size: 1rem;
      line-height: 1.1;
      display: block;
      padding: 0.5em;
    }
    &.level-3 {
      font-size: 0.8rem;
      line-height: 1.1;
      display: block;
      text-transform: none;
      letter-spacing: 0;
    }
  }
}

.Nav--account {
  margin-top: 3rem;
  .Nav__item {
    display: block;
    padding: 0.5em;
  }
}

// UTILITIES -----------------------------------------------------------------------
.Nav--utilities {
  width: 100%;
  .Nav__item {
    svg {
      display: block;
    }
    .Button{
      font-size: 1.5rem;
    }
  }
}

// VERTICAL -----------------------------------------------------------------------
.Nav--vertical {
  flex-direction: column;
}

// FOOTER -----------------------------------------------------------------------
.Nav--footer {
  //text-align: center;
  justify-content: space-around;
  align-items: flex-start;
  
  .Nav:not(.Nav--social) {
    display: block;
  }

  @include mq($until: desktop) {
    flex-flow: row wrap;
    .Nav__item--level1 {
      margin-bottom: 2rem;
    }
  }

  @include mq($until: tablet) {
    .Nav__item--level1 {
      flex-basis: 100%;
    }
  }
  @include mq($from: tablet, $until: desktop) {
    .Nav__item--level1 {
      flex-basis: 50%;
    }
  }

  .Nav__link {
    font-family: $f-sans;
    font-weight: 300;
    display: block;
    padding: 0.4em 0;
    &.level-1 {
      text-transform: uppercase;
      margin-bottom: 1em;
      letter-spacing: 0.2em;
      font-weight: 600;
    }
    
  }
  .Nav__submenu{
    .level-2 {
      font-size: 1rem;
      padding: 0;
      text-transform: none;
    }
  }
}

// SOCIAL -----------------------------------------------------------------------
.Nav--social {
  @include rfs(2rem);
  margin-top: 1em;
  margin-bottom: 1em;
  justify-content: center;
  @include mq($from: desktop){
    justify-content: flex-start;
  }
  .Nav__item{
    padding: 0 0.3em;
    &:first-child{
      padding-left: 0;
    }
  }
  .Nav__link{
    color: v(color-ui-secondary);
    font-size: 1.5rem;
    
  }
}

// PAGINATION -----------------------------------------------------------------
.Nav--pagination {
  margin-top: 6rem;
  justify-content: center;
  align-items: center;
  .page.current,
  .prev a,
  .next a,
  .page a {
    margin: 0.1em;
    display: inline-block;
    padding: 0.25em 0.55em;
    font-size: 1rem;
    font-family: $f-sans2;
    @include mq($from: tablet){
      padding: 0.25em 0.75em;
    }
  }
  .page.current {
    color: var(--color-ui-secondary);
    font-weight: 700;
    
  }
  .prev,
  .next {
    a {
      font-size: 0.8rem;
      letter-spacing: 0;
      white-space: nowrap;
      text-transform: uppercase;
    }
  }
  .prev {
    @include mq($from: tablet){
      margin-right: 2rem;
    }
  }
  .next {
    @include mq($from: tablet){
      margin-left: 2rem;
    }
  }
  .pages {
    display: flex;
    flex-flow: row wrap;
  }
  .page a {
    &:hover {
      // background: transparentize(gc(ui, copper), 0.9);
      color: gc(ui, copper);
    }
  }
}

// BREADCRUMBS -----------------------------------------------------------------
.Nav--breadcrumbs {
  font-size: 0.75rem;
  margin-top: 1rem;
  @include rfs(4rem,margin-bottom);
  justify-content: flex-start;
  ol {
    margin: 0;
    list-style: none;
    display: flex;
    flex-flow: row wrap;
    padding: 0;
  }
  .Nav__item {
    
    &:first-child {
      padding-left: 0;
    }
    a {
      font-family: $f-body;
      font-weight: 400;
    }
  }
}

// ----------------------------------------------------------------------------
// Social Sharing
// ----------------------------------------------------------------------------
.Nav--socialShare {
  justify-content: flex-start;
  a {
    font-size: 1.3rem;
    padding: 0.2em 0.3em;
    color: var(--color-ui-secondary);
  }
  [aria-hidden] {
    display: none;
  }
}

// ----------------------------------------------------------------------------
// Top nav
// ----------------------------------------------------------------------------

.Nav--top-nav {
  padding: 0.2em 0;
  @include mq($from: tablet){
    justify-content: flex-end;
  }
  .Nav__item{
    text-transform: uppercase;
    &:not(:last-child){
      border-right: 1px solid var(--color-ui-primary);
    }
  }

  .Nav__link{
    font-family: $f-sans2;
    font-weight: 400;
    font-size: 0.7rem;
    @include mq($from:tablet){
      font-size: 0.8rem;
    }
  }
}