main.Main {
  max-width: 100%;
  overflow-x: hidden;

  &.header_flower_deco_A,
  &.header_flower_deco_B {
    position: relative;
    &:before,
    &:after {
      content: "";
      position: absolute;
      @include rfs(20rem);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      z-index: -1;
    }
    &:before {
      width: 0.8em;
      height: 0.8em;
      left: 0;
      top: 0;
    }
    &:after {
      width: 0.9em;
      height: 0.8em;
      right: 0;
      top: 0;
    }
  }
  &.header_flower_deco_A {
    &:before {
      background-image: url("~Source/img/flower_deco_3.png");
    }
    &:after {
      background-image: url("~Source/img/flower_deco_2.png");
      transform: scaleX(-1);
    }
  }
  &.header_flower_deco_B {
    &:before {
      background-image: url("~Source/img/flower_deco_5.png");
      font-size: 5rem;
      @include mq($from: tablet) {
        font-size: 10rem;
      }
      @include mq($from: 1200px) {
        font-size: 20rem;
      }
      @include mq($from: desktopLarge) {
        font-size: 30rem;
      }
      width: 1em;
    }
    &:after {
      background-image: url("~Source/img/flower_deco_6.png");
      font-size: 5rem;
      @include mq($from: tablet) {
        font-size: 10rem;
      }
      @include mq($from: 1200px) {
        font-size: 20rem;
      }
      @include mq($from: desktopLarge) {
        font-size: 30rem;
      }
      width: 0.8em;
      height: 0.6em;
    }
  }
}

.Main__header {
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 3rem;
  padding: 1rem;
  text-align: center;
  .template-collection & {
    margin-top: 0;
  }
  h1 {
    margin: 0 0 0.2em 0;
    @include rfs(8rem);
  }
  form {
    display: flex;
    justify-content: center;
  }
  input[type="text"] {
    min-width: 300px;
    height: 3.1rem;
    @include mq($until: tablet) {
      min-width: auto;
    }
  }
  .subtitle {
    color: var(--color-text-pink);
    font-family: $f-display;
    text-transform: lowercase;
    @include rfs(5.5rem);
    margin-top: -0.3em;
  }
  p {
    max-width: 54em;
    margin-left: auto;
    margin-right: auto;
  }
}

.Main__content {
  padding-bottom: 3rem;
}

body div #smile-ui-container {
  position: relative !important;
  width: 100% !important;
  right: 5rem !important;
  z-index: 1 !important;
  bottom: 0.7rem !important;
}

.smile-launcher-frame-container {
  position: absolute !important;
}
.Product,
.Card {
  .swym-button {
    background-color: #fff !important;
    border-radius: 50% !important;
  }
}

.lazyload {
  opacity: 0;
}

#swym-anchor[data-position="left"] {
  display: none;
  .template-collection &,
  .template-product & {
    display: block;
  }
  #notepad-anchor-title {
    padding: 0 15px 0 22px !important;
    height: 3rem !important;
  }
}
#loyalty {
  .Main__header {
    display: none;
  }
}

#powrIframeLoader {
  display: none;
}
